import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Panel, PanelHeader } from '../../components/panel/panel.jsx';
import { Button, Modal, ModalBody, ModalHeader, ModalFooter } from 'reactstrap';
import { Table } from '../../components/table/reactTable';
import { getData, deleteData } from '../../services/Utils/DB/DB';
import { notification, Spin } from 'antd';

const Policies = () => {
  const [rows, setRows] = useState([]);
  const [modalshow, setmodalshow] = useState(false);
  const [ID, setID] = useState();
  const [focusAfterClose, setFocusAfterClose] = useState(true);
  const [loading, setloading] = useState(false);


  const utoken = localStorage.getItem('utoken') || '';

  useEffect(() => {
    setloading(!loading);
    getData('/policies', utoken)
      .then((data) => {
        if (data) {
          setRows(data);
          setloading(loading);
          console.log(data);
        }
      })
      .catch((error) => {
        setloading(loading);
        console.log(error);
      });
  }, []);

  const columns = React.useMemo(
    () => [
      {
        Header: '#',
        columns: [
          {
            Header: '#',
            accessor: '#',
            sortable: false,
          },
        ],
      },
      {
        Header: 'Title',
        columns: [
          {
            Header: 'Title',
            accessor: 'title',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Version',
        columns: [
          {
            Header: 'Version',
            accessor: 'version',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Updated',
        columns: [
          {
            Header: 'Updated',
            accessor: 'updatedAt',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Status',
        columns: [
          {
            Header: 'Status',
            accessor: 'status',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Edit',
        columns: [
          {
            Header: '',
            accessor: 'edit',
            sortable: false,
            Cell: ({ row }) => {
              return (
                <span className="edit">
                  <a className="btn btn-danger btn-icon btn-circle btn-sm">
                    <i className="fas fa-trash-alt" onClick={() => OnDeleteRow(row.index)}></i>
                  </a>
                </span>
              );
            },
          },
        ],
      },
    ],
    []
  );

  const OnDeleteRow = (id) => {
    setID(id);
    setmodalshow(true);
  };

  const OnConfirmDeleteRow = () => {
    setloading(true);
    console.log(rows[ID].policyId);

    deleteData(`/policies?policyId=${rows[ID].policyId}`, utoken)
      .then((data) => {
        if (data) {
          // setRows(data);
          console.log(data);
          setRows([...rows.filter((item, index) => item !== rows[ID])]);
          console.log(data);
          setloading(false);
          notification.success({
            message: <h1>Notification</h1>,
            description: `success:Admin successfully removed from the policy list.`,
            placement: 'bottomRight',
          });
        }
      })
      .catch((error) => {
        setloading(false);
        notification.error({
          message: <h1>error</h1>,
          description: `${error}`,
          placement: 'bottomRight',
        });
        console.log(error);
      });

    setmodalshow(false);
  };


  return (
    <div>
      <ol className="breadcrumb float-xl-right">
        <li className="breadcrumb-item">
          <Link to="/">Dashboard</Link>
        </li>
        <li className="breadcrumb-item">
          <Link to="/policy/view">Policies</Link>
        </li>
        <li className="breadcrumb-item active">View Policies</li>
      </ol>
      <h1 className="page-header">
        Policies <small>manage and approve the policy here.</small>
      </h1>
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={modalshow}>
        <ModalHeader toggle={() => setmodalshow(false)}>Warning</ModalHeader>
        <ModalBody>
          <div> Do you want to delete this data?</div>
        </ModalBody>
        <ModalFooter>
          <Button color="primary" className="pull-right" onClick={OnConfirmDeleteRow}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={() => setmodalshow(false)}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
      <Panel>
        <PanelHeader>All Policies Lists</PanelHeader>
        <div className="table-responsive">
          <Table columns={columns} data={rows} />
        </div>        
      </Panel>
    </div>
  );
};

export default Policies;
