import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { Panel, PanelBody, PanelFooter, PanelHeader } from '../../components/panel/panel';
import { Button, Input, FormGroup, Label, Form, Row, Col } from 'reactstrap';
import { postData } from '../../services/Utils/DB/DB';
import { notification } from 'antd';
import { useHistory } from 'react-router-dom';
import axios from "axios";
export const AddSubscriptionPackage = () => {
  const [Name, subscriptionTypeDisplayName] = useState('');
  const [Type, subscriptionType] = useState('');
  const [Duration, setDuration] = useState('');
  const [Price, setPrice] = useState('');
  const history = useHistory();

  const onChangeName = (e) => {
    const Name = e.target.value;
    subscriptionTypeDisplayName(Name);
  };

  const onChangeType = (e) => {
    const Type = e.target.value;
    subscriptionType(Type);
  };

  const onChangePrice = (e) => {
    const Price = e.target.value;
    setPrice(Price);
  };

  const onChangeDuration = (e) => {
    const Duration = e.target.value;
    setDuration(Duration);
  };

  const AddSubscriptionPackage = (e) => {
    e.preventDefault();
    if ((Name === '') | (Type === '') | (Price === '') | (Duration === '')) {
      return notification.warn({
        message: <h1>Notification</h1>,
        description: `warn: complete all the fields to submit form.`,
        placement: 'bottomRight',
      });
    }
    const token = localStorage.getItem('utoken');

    const prices = [];
    if (Price) {
      prices.push({
        Duration: Duration,
        Price: Price,
      });
    }

    const form_data = {
      subscriptionTypeDisplayName: Name,
      subscriptionType: Type,
      prices: prices
    };

    postData('/subscriptiontypes',JSON.stringify(form_data), token)
      .then((response) => {
        notification.success({
          message: <h1>Notification</h1>,
          description: `success: ${Name}  successfully added to the subscription packages list.`,
          placement: 'bottomRight',
        });
        history.push('/subscription/view');
      })
      .catch((error) => {
        notification.error({
          message: <h1 style={{ color: 'red' }}>Error</h1>,
          description: `${error}.`,
          placement: 'bottomRight',
        });
      });
  };

  return (
    <div>
      <ol className="breadcrumb float-xl-right">
        <li className="breadcrumb-item">
          <Link to="/">Home</Link>
        </li>
        <li className="breadcrumb-item">
          <Link to="/subscription/view">Subscription Packages</Link>
        </li>
        <li className="breadcrumb-item active">Add Subscription Package</li>
      </ol>
      <h1 className="page-header">
        Add Subscription Package <small>add a new subscription package here.</small>
      </h1>

      <Panel>
        <PanelHeader noButton={true}>Add Subscription Packages Form</PanelHeader>
        <PanelBody>
          <Row>
            <Col md="8">
              <div className="card">
                <div className="card-body">
                  <Form>
                    <Row>
                      <Col>
                        <FormGroup>
                          <Label for="name">Name</Label>
                          <Input type="text" name="name" id="name" value={Name} onChange={onChangeName} placeholder="Premium" />
                        </FormGroup>
                      </Col>
                    </Row>
                    <Row>
                      <Col>
                        <FormGroup>
                          <Label for="Type">Subscription Type</Label>
                          <Input type="text" name="Type" id="Type" value={Type} onChange={onChangeType} placeholder="Months" />
                        </FormGroup>
                      </Col>
                    </Row>
                    <Row>
                      <Col>
                        <FormGroup>
                          <Label for="duration">Duration</Label>
                          <Input type="text" name="duration" id="duration" value={Duration} onChange={onChangeDuration} placeholder="365" />
                        </FormGroup>
                      </Col>
                      <Col>
                        <FormGroup>
                          <Label for="price">Price</Label>
                          <Input type="text" name="price" id="price" value={Price} onChange={onChangePrice} placeholder="999" />
                        </FormGroup>
                      </Col>                
                      
                    </Row>
                  </Form>
                </div>
                <div className="card-footer">
                  <Row>
                    <Col>
                      <div className="pull-right">
                        <button onClick={AddSubscriptionPackage} className="btn btn-primary mr-3">
                          Submit
                        </button>
                        <button
                          className="btn btn-secondary"
                          onClick={() => history.push('/subscription/view')}
                        >
                          Cancel
                        </button>
                      </div>
                    </Col>
                  </Row>
                </div>
              </div>
            </Col>
          </Row>
        </PanelBody>
      </Panel>
    </div>
  );
};
