import React, { useState, useEffect } from 'react';
import { Button, Modal, ModalBody, ModalFooter, Label, Input, FormGroup, Form, ModalHeader } from 'reactstrap';
import { useTable, useSortBy, usePagination } from 'react-table';
import { Link } from 'react-router-dom';
import { Panel, PanelHeader, PanelBody } from './../../components/panel/panel.jsx';
import makeData from './../make-data';
import { getData, postData, deleteData } from './../../services/Utils/DB/DB';
import { notification, Spin } from 'antd';
import { Table } from '../../components/table/reactTable';
import { addNotification } from '../../components/ui-notifications';
const ManageLanguages = () => {
  const [rows, setRows] = useState([]);
  const [reloadStatus, setReloadStatus] = useState(0);
  const [loading, setloading] = useState(false);
  const utoken = localStorage.getItem('utoken') || '';
  const [modalshow, setmodalshow] = useState(false);
  const [ID, setID] = useState();

  useEffect(() => {
    setloading(true);
    getData('/languages', utoken)
      .then((data) => {
        if (data) {
          const Arr = [];
          Object.values(data).map((i) => {
            const item = {
              language: i.lang,
              navtiveLanguage: i.nativeLang,
              contentCreator: i.createdBy,
              dateCreated: i.createdAt,
              langId: i.langId,
            };
            // console.log(Arr);
            Arr.push(item);
          });
          setRows(Arr);
          setloading(false);
          console.log('Manage', data);
        }
      })
      .catch((error) => {
        setloading(false);
        console.log(error);
      });
  }, [reloadStatus]);

  const [languageTitle, setLanguageTitle] = useState('');
  const onChangeLanguageTitle = (e) => {
    const languageTitle = e.target.value;
    setLanguageTitle(languageTitle);
  };
  const [nativeLanguage, setNativeLanguage] = useState('');
  const onChangeNativeLanguage = (e) => {
    const nativeLanguage = e.target.value;
    setNativeLanguage(nativeLanguage);
  };
  const addLanguage = (e) => {
    e.preventDefault();
    setloading(true);
    const token = localStorage.getItem('utoken');
    const form_data = {
      lang: languageTitle,
      nativeLang: nativeLanguage,
    };
    postData('/languages', JSON.stringify(form_data), token)
      .then((response) => {
        setloading(false);
        addNotification('success', 'ADDED', 'Language successfully added to the language list.', 'bottom-right');
        setReloadStatus((prev) => prev + 1);
      })
      .catch((error) => {
        setloading(false);
        addNotification('danger', 'Error', error, 'bottom-right');
      });
    toggle();
  };

  const OnDeleteRow = (id) => {
    console.log(id);
    setID(id);
    setmodalshow(true);
  };

  const deleteLanguage = (e, id) => {
    e.preventDefault();
    setloading(true);
    const token = localStorage.getItem('utoken');
    deleteData(`/languages?langId=${rows[ID].langId}`, token)
      .then((response) => {
        setloading(false);
        addNotification('danger', 'DELETED', 'Language successfully added to the language list.', 'bottom-right');
        setReloadStatus((prev) => prev + 1);
      })
      .catch((error) => {
        setloading(false);
        addNotification('danger', 'Error', error, 'bottom-right');
      });
    setmodalshow(false);
  };
  const [open, setOpen] = useState(false);
  const [focusAfterClose, setFocusAfterClose] = useState(true);

  const toggle = () => setOpen(!open);

  const columns = React.useMemo(
    () => [
      {
        Header: 'Language',
        columns: [
          {
            Header: 'Language',
            accessor: 'language',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Native Language',
        columns: [
          {
            Header: 'Natuve Language',
            accessor: 'navtiveLanguage',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Content Creator',
        columns: [
          {
            Header: 'Content Creator',
            accessor: 'contentCreator',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Date Of Creation',
        columns: [
          {
            Header: 'Date Of Creation',
            accessor: 'dateCreated',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Edit',
        columns: [
          {
            Header: '',
            accessor: 'edit',
            sortable: false,
            Cell: ({ row }) => {
              return (
                <span className="edit">
                  {/* <a className="btn btn-primary btn-icon btn-circle btn-sm">
                    <i className="fas fa-pencil-alt" onClick={() => OnEditRow(row.index)}></i>
                  </a> */}
                  <a className="btn btn-danger btn-icon btn-circle btn-sm">
                    <i className="fas fa-trash-alt" onClick={() => OnDeleteRow(row.index)}></i>
                  </a>
                </span>
              );
            },
          },
        ],
      },
    ],
    []
  );

  return (
    <div>
      <ol className="breadcrumb float-xl-right">
        <li className="breadcrumb-item">
          <Link to="/">Dashboard</Link>
        </li>
        <li className="breadcrumb-item">
          <Link to="/content-management/video-category">Video Languages</Link>
        </li>
        <li className="breadcrumb-item active">View Languages</li>
      </ol>
      <h1 className="page-header">
        Video Languages <small>manage and edit the Video Languages here.</small>
      </h1>
      <Panel>
        <PanelHeader noButton={true}>
          All Video Languages Lists
          <Button color="default" size="xs" className="mr-2 rounded-0 pull-right" onClick={toggle}>
            Add Language
          </Button>
        </PanelHeader>
        <div className="table-responsive">
          <Table columns={columns} data={rows} />
        </div>
      </Panel>
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={open}>
        <ModalHeader toggle={toggle}>Add Language</ModalHeader>
        <ModalFooter>
          <FormGroup className="w-100">
            <Label for="languageTitle">Language Title</Label>
            <Input type="text" name="languageTitle" id="languageTitle" value={languageTitle} onChange={onChangeLanguageTitle} />
          </FormGroup>
          <FormGroup className="w-100">
            <Label for="nativeLanguage">Native Language</Label>
            <Input
              type="text"
              name="nativeLanguage"
              id="nativeLanguage"
              value={nativeLanguage}
              onChange={onChangeNativeLanguage}
            />
          </FormGroup>
          <Button color="primary" className="pull-right" onClick={addLanguage}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={toggle}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={modalshow}>
        <ModalHeader toggle={() => setmodalshow(false)}>Warning</ModalHeader>
        <ModalBody>
          <div> Do you want to delete this data?</div>
        </ModalBody>
        <ModalFooter>
          <Button color="primary" className="pull-right" onClick={deleteLanguage}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={() => setmodalshow(false)}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
    </div>
  );
};

export default ManageLanguages;
