import React, { useState, useEffect } from 'react';
import { useTable, useSortBy, usePagination } from 'react-table';
import { Link } from 'react-router-dom';
import { Panel, PanelHeader, PanelBody } from '../../components/panel/panel.jsx';
import { Button, Input, FormGroup, Label, Form, Row, Col, Modal, ModalBody, ModalHeader, ModalFooter } from 'reactstrap';
import makeData from '../make-data';
import { getData, deleteData, patchData } from '../../services/Utils/DB/DB';
import { notification, Spin } from 'antd';
import InputMask from 'react-input-mask';
import { addNotification } from '../../components/ui-notifications';

export const Invoices = () => {
  const [rows, setRows] = useState([]);
  const [modalshow, setmodalshow] = useState(false);
  const [Editmsg, setEditmsg] = useState(false);
  const [ID, setID] = useState();
  const [invoiceID, setinvoiceID] = useState('');
  const [Title, setTitle] = useState('');
  const [Amount, setAmount] = useState('');
  const [Status, setStatus] = useState('');
  const [focusAfterClose, setFocusAfterClose] = useState(true);
  const [loading, setloading] = useState(false);

  const onChangeinvoiceID = (e) => {
    const first = e.target.value;
    setinvoiceID(first);
  };
  const onChangeTitle = (e) => {
    const last = e.target.value;
    setTitle(last);
  };
  const onChangeAmount = (e) => {
    const Amount = e.target.value;
    setAmount(Amount);
  };
  const onChangeStatus = (e) => {
    const Status = e.target.value;
    setStatus(Status);
  };

  const utoken = localStorage.getItem('utoken') || '';
  useEffect(() => {
    setloading(!loading);
    getData('/invoices', utoken)
      .then((data) => {
        if (data) {
          setRows(data);
          setloading(loading);
          console.log(data);
        }
      })
      .catch((error) => {
        setloading(loading);
        console.log(error);
      });
  }, []);

  const columns = React.useMemo(
    () => [
      {
        Header: 'ID',
        columns: [
          {
            Header: 'ID',
            accessor: 'invoiceID',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Title',
        columns: [
          {
            Header: 'Title',
            accessor: 'Title',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Amount',
        columns: [
          {
            Header: 'Amount',
            accessor: 'Amount',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Status',
        columns: [
          {
            Header: 'Status',
            accessor: 'Status',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Edit',
        columns: [
          {
            Header: '',
            accessor: 'edit',
            sortable: false,
          },
        ],
      },
    ],
    []
  );

  const data = React.useMemo(() => makeData(2000), []);

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    page,

    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,
    state: { pageIndex, pageSize },
  } = useTable({ columns, data, initialState: { pageIndex: 2  } }, useSortBy, usePagination);

  const OnDeleteRow = (id) => {
    setID(id);
    setmodalshow(true);
    console.log(rows[id].uid);
  };

  const OnConfirmDeleteRow = () => {
    setloading(true);
    console.log(rows[ID].uid);

    deleteData(`/admins?uid=${rows[ID].uid}`, utoken)
      .then((data) => {
        if (data) {
          // setRows(data);
          console.log(data);
          setRows([...rows.filter((item, index) => item !== rows[ID])]);
          console.log(data);
          setloading(false);
          addNotification('danger', 'DELETED', 'Admin successfully deleted from the Admin list.', 'bottom-right');
        }
      })
      .catch((error) => {
        setloading(false);
        addNotification('danger', 'Error', error, 'bottom-right');
        console.log(error);
      });

    setmodalshow(false);
  };

  const OnEditRow = async (id) => {
    setID(id);
    console.log(rows[id]);
    const name = rows[id].displayName.split(' ');
    setinvoiceID(name[0]);
    setTitle(name[1]);
    setAmount(rows[id].Amount);
    setStatus(rows[id].StatusNumber);
    setEditmsg(true);
  };

  const OnConfirmEditRow = (e) => {
    e.preventDefault();
    if ((invoiceID === '') | (Title === '') ) {
      return notification.warn({
        message: <h1>Notification</h1>,
        description: `warn: complete all the fields to submit form.`,
        placement: 'bottomRight',
      });
    }
    setloading(true);
    var form_data = {
      invoiceID: invoiceID,
      Title: Title, 
      uid: rows[ID].uid,
    };
    console.log(form_data);
    patchData(`/admins`, form_data, utoken)
      .then(({ data }) => {
        if (data) {
          let clone = [...rows];
          clone[ID] = data;
          setRows(clone);
          console.log(data);
          setloading(false);
          addNotification('warning', 'EDITED', 'Invoice successfully deleted.', 'bottom-right');
        }
      })
      .catch((error) => {
        setloading(false);
        addNotification('danger', 'Error', error, 'bottom-right');
        console.log(error.response);
      });
    setEditmsg(false);
  };

  return (
    <div>
      <ol className="breadcrumb float-xl-right">
        <li className="breadcrumb-item">
          <Link to="/">Dashboard</Link>
        </li>
        <li className="breadcrumb-item">
          <Link to="/invoices/viewInvoices">Invoices</Link>
        </li>
        <li className="breadcrumb-item active">View Invoices</li>
      </ol>
      <h1 className="page-header">
        Invoices <small>manage and approve the invoices here.</small>
      </h1>
      <Modal className="modal-lg" returnFocusAfterClose={focusAfterClose} isOpen={Editmsg}>
        <ModalHeader toggle={() => setEditmsg(false)}>Edit Admin Form</ModalHeader>
        <ModalFooter>
          <Row style={{ width: '100%' }}>
            <Col>
              <Form>
                <Row>
                  <Col>
                    <FormGroup>
                      <Label for="first">First Name</Label>
                      <Input
                        type="text"
                        name="first"
                        id="first"
                        // placeholder="Product Name"
                        value={invoiceID}
                        onChange={onChangeinvoiceID}
                        // className={!isInvalidName ? "" : "is-invalid"}
                      />
                    </FormGroup>
                  </Col>
                  <Col>
                    <FormGroup>
                      <Label for="last">Last Name</Label>
                      <Input
                        type="text"
                        name="last"
                        id="last"
                        // placeholder="Product Name"
                        value={Title}
                        onChange={onChangeTitle}
                        // className={!isInvalidName ? "" : "is-invalid"}
                      />
                    </FormGroup>
                  </Col>
                </Row>
                <Row>
                  <Col md={8}>
                    <FormGroup>
                      <Label for="Amount">Amount</Label>
                      <Input
                        type="Amount"
                        name="Amount"
                        id="Amount"
                        // placeholder="Product Name"
                        value={Amount}
                        disabled
                        // onChange={onChangeAmount}
                        // className={!isInvalidName ? "" : "is-invalid"}
                      />
                    </FormGroup>
                  </Col>
                  <Col>
                    <FormGroup>
                      <Label for="Status">Status No.</Label>
                      <InputMask
                        id="Status"
                        mask="+\91 999 999 9999"
                        maskChar="_"
                        className="form-control"
                        value={Status}
                        // onChange={onChangeStatus}
                        disabled
                      ></InputMask>
                    </FormGroup>
                  </Col>
                </Row>
              </Form>
              <div className="card-footer">
                <Row>
                  <Col>
                    <div className="pull-right">
                      <button onClick={OnConfirmEditRow} className="btn btn-primary mr-3">
                        Submit
                      </button>
                      <button onClick={() => setEditmsg(false)} className="btn btn-secondary">
                        Cancel
                      </button>
                    </div>
                  </Col>
                </Row>
              </div>
            </Col>
          </Row>
        </ModalFooter>
      </Modal>
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={modalshow}>
        <ModalHeader toggle={() => setmodalshow(false)}>Warning</ModalHeader>
        <ModalBody>
          <div> Do you want to delete this data?</div>
        </ModalBody>
        <ModalFooter>
          <Button color="primary" className="pull-right" onClick={OnConfirmDeleteRow}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={() => setmodalshow(false)}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
      <Panel>
        <PanelHeader>All Invoice Lists</PanelHeader>
        <div className="table-responsive">
          <PanelBody>
            {/* {loading ? (  */}
            {/* <span className="spinner-small"></span>
            {/* ) : ( */}
            <table className="table table-striped table-bordered" {...getTableProps()}>
              <thead>
                {headerGroups.map((headerGroup) => (
                  <tr {...headerGroup.getHeaderGroupProps()}>
                    {headerGroup.headers.map((column) => (
                      <th {...column.getHeaderProps(column.getSortByToggleProps())}>
                        <div className="d-flex" style={{ minWidth: '1%' }}>
                          <span>{column.render('Header')}</span>
                          <span className="ml-auto">
                            {column.sortable ? (
                              column.isSorted ? (
                                column.isSortedDesc ? (
                                  <i className="fa fa-sort-down fa-fw f-s-14 text-blue"></i>
                                ) : (
                                  <i className="fa fa-sort-up fa-fw f-s-14 text-blue"></i>
                                )
                              ) : (
                                <i className="fa fa-sort fa-fw f-s-14 opacity-3"></i>
                              )
                            ) : (
                              ''
                            )}
                          </span>
                        </div>
                      </th>
                    ))}
                  </tr>
                ))}
              </thead>
              {rows && (
                <tbody {...getTableBodyProps()}>
                  {Object.keys(rows).length <= 0 ? (
                    <tr>
                        {console.log(rows)}
                      <td colSpan={5} style={{ textAlign: 'center', fontWeight: 'bold', fontSize: '26px' }}>
                        <span>No matching records found</span>
                      </td>
                    </tr>
                  ) : (
                    Object.keys(rows).map((id) => {
                      return (
                        <tr key={rows[id].displayName}>
                          <td>{rows[id].displayName}</td>
                          <td>{rows[id].Amount}</td>
                          <td>{rows[id].StatusNumber}</td>
                          <td>
                            {rows[id].AmountVerified ? (
                              <span className="label label-green">Confirmed</span>
                            ) : (
                              <span className="label label-danger">Not Confirmed</span>
                            )}
                          </td>
                          <td className="edit">
                            <a className="btn btn-primary btn-icon btn-circle btn-sm">
                              <i className="fas fa-pencil-alt" onClick={() => OnEditRow(id)}></i>
                            </a>
                            <a className="btn btn-danger btn-icon btn-circle btn-sm">
                              <i className="fas fa-trash-alt" onClick={() => OnDeleteRow(id)}></i>
                            </a>
                          </td>
                        </tr>
                      );
                    })
                  )}
                </tbody>
              )}
            </table>
            {/* )} */}
          </PanelBody>
        </div>
        <hr className="m-0" />
        {Object.keys(rows).length <= 0 ? null : (
          // <PanelBody>
          <div className="d-flex align-items-center justify-content-center">
            <ul className="pagination mb-0">
              <li className="page-item">
                <button className="page-link" onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
                  <i className="fa fa-angle-double-left"></i>
                </button>
              </li>
              <li className="page-item">
                <button className="page-link" onClick={() => previousPage()} disabled={!canPreviousPage}>
                  <i className="fa fa-angle-left"></i>
                </button>
              </li>
              <li className="page-item d-flex align-items-center px-2">
                <div>
                  Page
                  <strong>
                    {pageIndex + 1} of {pageOptions.length}
                  </strong>
                </div>
              </li>
              <li className="page-item">
                <button className="page-link" onClick={() => nextPage()} disabled={!canNextPage}>
                  <i className="fa fa-angle-right"></i>
                </button>
              </li>
              <li className="page-item">
                <button className="page-link" onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
                  <i className="fa fa-angle-double-right"></i>
                </button>
              </li>
            </ul>
            <div className="ml-3 mr-1">Go to page:</div>
            <div className="width-50 mx-2">
              <input
                className="form-control"
                type="number"
                defaultValue={pageIndex + 1}
                onChange={(e) => {
                  const page = e.target.value ? Number(e.target.value) - 1 : 0;
                  gotoPage(page);
                }}
              />
            </div>
            <div>
              <select
                className="form-control"
                value={pageSize}
                onChange={(e) => {
                  setPageSize(Number(e.target.value));
                }}
              >
                {[10, 20, 30, 40, 50].map((pageSize) => (
                  <option key={pageSize} value={pageSize}>
                    Show {pageSize}
                  </option>
                ))}
              </select>
            </div>
          </div>
          // </PanelBody>
        )}
      </Panel>
    </div>
  );
};
 