import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Panel, PanelBody, PanelFooter, PanelHeader } from '../../components/panel/panel';
import { Button, Input, FormGroup, Label, Form, Row, Col } from 'reactstrap';
import { patchData, getData } from '../../services/Utils/DB/DB';
import { useHistory } from 'react-router-dom';
import { notification, Spin } from 'antd';
import { addNotification } from '../../components/ui-notifications';
export const EditPolicy = () => {
  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const utoken = localStorage.getItem('utoken') || '';
  const [loading, setloading] = useState(false);
  const [rows, setRows] = useState([]);
  const [ID, setID] = useState(JSON.parse(localStorage.getItem('policyEditID')));

  const history = useHistory();

  useEffect(() => {
    setloading(true);
    getData('/policies', utoken)
      .then((data) => {
        if (data) {
          setloading(false);
          setRows(data);
        }
      })
      .catch((error) => {
        setloading(false);
        history.push('/page/view-policies');
        console.log(error);
      });
  }, []);

  // console.log(rows)
  const onChangeTitle = (e) => {
    const title = e.target.value;
    setTitle(title);
  };
  const onChangeContent = (e) => {
    const content = e.target.value;
    setContent(content);
  };

  const OnConfirmEditRow = (e) => {
    e.preventDefault();
    if ((content === '') | (title === '')) {
      return notification.warn({
        message: <h1>Notification</h1>,
        description: `warn: complete all the fields to submit form.`,
        placement: 'bottomRight',
      });
    }

    setloading(true);
    // console.log("ID:",ID)
    // console.log("row:",rows[ID])
    // console.log('title',rows[ID].title)
    var form_data = {
      policyId: rows[ID].policyId,
      // title: title,
      content: content,
      updateType: 'major',
    };

    patchData(`/policies`, form_data, utoken)
      .then(({ data }) => {
        if (data) {
          setloading(false);
          console.log(data);
          //   let clone = [...rows];
          //   clone[ID] = data;
          //   setRows(clone);
          //   console.log(data);
          history.push('/page/view-policies');
          addNotification('warning', 'EDITED', 'Policy info successfully edited.', 'bottom-right');
        }
      })
      .catch((error) => {
        history.push('/page/view-policies');
        addNotification('danger', 'Error', error, 'bottom-right');
      });
  };

  const OnCancelEdit = () => {
    history.push('/page/view-policies');
  };
  return (
    <Spin spinning={loading}>
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="/">Dashboard</Link>
          </li>
          <li className="breadcrumb-item">
            <Link to="/page/view-policies">Policies</Link>
          </li>
          <li className="breadcrumb-item active">Edit Policy</li>
        </ol>
        <h1 className="page-header">
          Edit Policy <small>Edit a policy here.</small>
        </h1>

        <Panel>
          <PanelHeader noButton={true}>Edit Policy Form</PanelHeader>
          <PanelBody>
            <Row>
              <Col md="12">
                <div className="card">
                  <div className="card-body">
                    <Form>
                      {/* <Row>
                        <Col>
                          <FormGroup>
                            <Label for="title">Title</Label>
                            <Input
                              type="text"
                              name="title"
                              id="title"
                              // placeholder="Title"
                              value={title}
                              onChange={onChangeTitle}
                              // className={!isInvalidName ? "" : "is-invalid"}
                            />
                          </FormGroup>
                        </Col>
                      </Row> */}
                      <Row>
                        <Col>
                          <FormGroup>
                            <Label for="content">Description</Label>
                            <Input
                              type="textarea"
                              name="content"
                              id="content"
                              rows="10"
                              // placeholder="Product Name"
                              value={content}
                              onChange={onChangeContent}
                              // className={!isInvalidName ? "" : "is-invalid"}
                            />
                          </FormGroup>
                        </Col>
                      </Row>
                    </Form>
                  </div>
                  <div className="card-footer">
                    <Row>
                      <Col>
                        <div className="pull-right">
                          <button onClick={OnConfirmEditRow} className="btn btn-primary mr-3">
                            Submit
                          </button>
                          <button onClick={OnCancelEdit} className="btn btn-secondary">
                            Cancel
                          </button>
                        </div>
                      </Col>
                    </Row>
                  </div>
                </div>
              </Col>
            </Row>
          </PanelBody>
        </Panel>
      </div>
    </Spin>
  );
};
