import React, { useState, useEffect } from 'react';
import { Button, Modal, ModalBody, ModalFooter, Label, Input, FormGroup, Form, ModalHeader } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Panel, PanelHeader, PanelBody } from './../../components/panel/panel.jsx';
import { getData, postData, deleteData, patchData } from '../../services/Utils/DB/DB';
import { notification, Spin } from 'antd';
import { Table } from '../../components/table/reactTable';
import { addNotification } from '../../components/ui-notifications';
const VideoGenre = () => {
  const [rows, setRows] = useState([]);
  const [reloadStatus, setReloadStatus] = useState(0);
  const utoken = localStorage.getItem('utoken') || '';
  const [modalshow, setmodalshow] = useState(false);
  const [Editmsg, setEditmsg] = useState(false);
  const [loading, setloading] = useState(false);
  const [ID, setID] = useState();
  useEffect(() => {
    setloading(true);
    getData('/genres', utoken)
      .then((data) => {
        if (data) {
          console.log('i am called');
          const Arr = [];
          Object.values(data).map((i) => {
            const item = {
              genre: i.genre,
              contentCreator: i.createdBy,
              dateCreated: i.createdAt,
              genreId: i.genreId,
            };
            // console.log(Arr);
            Arr.push(item);
          });
          setRows(Arr);
          setloading(false);
        }
      })
      .catch((error) => {
        setloading(false);
        console.log(error);
      });
  }, [reloadStatus]);

  const [genre, setGenre] = useState('');
  const onChangeGenre = (e) => {
    const genre = e.target.value;
    setGenre(genre);
  };
  const addGenre = (e) => {
    e.preventDefault();
    setloading(true);
    const token = localStorage.getItem('utoken');
    const form_data = {
      genre: genre,
    };
    postData('/genres', JSON.stringify(form_data), token)
      .then((response) => {
        setloading(false);
        addNotification('success', 'ADDED', 'Genres successfully added to the Genres list.', 'bottom-right');
        setReloadStatus((prev) => prev + 1);
      })
      .catch((error) => {
        setloading(false);
        addNotification('danger', 'Error', error, 'bottom-right');
      });
    toggle();
  };

  const OnEditRow = async (id) => {
    setID(id);
    setEditmsg(true);
  };

  const OnConfirmEditRow = (e) => {
    e.preventDefault();
    if (genre === '') {
      return notification.warn({
        message: <h1>Notification</h1>,
        description: `warn: complete all the fields to submit form.`,
        placement: 'bottomRight',
      });
    }
    setloading(true);

    var form_data = {
      genreId: rows[ID].genreId,
      genre: genre,
    };
    console.log(form_data);
    patchData(`/genres`, form_data, utoken)
      .then(({ data }) => {
        if (data) {
          let clone = [...rows];
          clone[ID] = data;
          setRows(clone);
          console.log(data);
          setloading(false);

          addNotification('warning', 'EDITED', 'Genres info successfully edited.', 'bottom-right');
        }
      })
      .catch((error) => {
        setloading(false);
        addNotification('danger', 'Error', error, 'bottom-right');
        console.log(error.response);
      });
    setEditmsg(false);
  };

  const OnDeleteRow = (id) => {
    console.log(id);
    setID(id);
    setmodalshow(true);
  };

  const deleteGenre = () => {
    const token = localStorage.getItem('utoken');
    setloading(true);
    console.log(rows[ID].genreId);
    deleteData(`/genres?genreId=${rows[ID].genreId}`, token)
      .then((response) => {
        setRows([...rows.filter((item, index) => item !== rows[ID])]);
        setloading(false);
        addNotification('danger', 'DELETED', 'Genres successfully deleted from the Genres list.', 'bottom-right');
        setReloadStatus((prev) => prev + 1);
      })
      .catch((error) => {
        setloading(false);
        addNotification('danger', 'Error', error, 'bottom-right');
        setReloadStatus((prev) => prev + 1);
      });
    setmodalshow(false);
  };

  const columns = React.useMemo(
    () => [
      {
        Header: 'Genre',
        columns: [
          {
            Header: 'Genre',
            accessor: 'genre',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Content Creator',
        columns: [
          {
            Header: 'Content Creator',
            accessor: 'contentCreator',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Date Of Creation',
        columns: [
          {
            Header: 'Date Of Creation',
            accessor: 'dateCreated',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Edit',
        columns: [
          {
            Header: 'Action',
            accessor: 'edit',
            sortable: false,
            Cell: ({ row }) => {
              return (
                <span className="edit">
                  <a className="btn btn-primary btn-icon btn-circle btn-sm">
                    <i className="fas fa-pencil-alt" onClick={() => OnEditRow(row.index)}></i>
                  </a>
                  <a className="btn btn-danger btn-icon btn-circle btn-sm">
                    <i className="fas fa-trash-alt" onClick={() => OnDeleteRow(row.index)}></i>
                  </a>
                </span>
              );
            },
          },
        ],
      },
    ],
    []
  );

  const [open, setOpen] = useState(false);
  const [focusAfterClose, setFocusAfterClose] = useState(true);

  const toggle = () => {
    setOpen(!open);
    setGenre('');
  };

  return (
    <div>
      <ol className="breadcrumb float-xl-right">
        <li className="breadcrumb-item">
          <Link to="/">Dashboard</Link>
        </li>
        <li className="breadcrumb-item">
          <Link to="/content-management/video-genre">Genres</Link>
        </li>
        <li className="breadcrumb-item active">View Genres</li>
      </ol>
      <h1 className="page-header">
        Genres <small>manage and approve the genres here.</small>
      </h1>
      <Panel>
        <PanelHeader noButton={true}>
          All Genres Lists
          <Button color="default" size="xs" className="mr-2 rounded-0 pull-right" onClick={toggle}>
            Add Genre
          </Button>
        </PanelHeader>

        <div className="table-responsive">
          <Table columns={columns} data={rows} />
        </div>
        <hr className="m-0" />
      </Panel>
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={open}>
        <ModalHeader toggle={toggle}>Add Genre</ModalHeader>
        <ModalFooter>
          <FormGroup className="w-100">
            <Label for="genre">Genre</Label>
            <Input type="text" name="genre" id="genre" value={genre} onChange={onChangeGenre} />
          </FormGroup>
          <Button color="primary" className="pull-right" onClick={addGenre}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={toggle}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={Editmsg}>
        <ModalHeader toggle={() => setEditmsg(false)}>Edit Genre</ModalHeader>
        <ModalFooter>
          <FormGroup className="w-100">
            <Label for="genre">Genre</Label>
            <Input type="text" name="genre" id="genre" value={genre} onChange={onChangeGenre} />
          </FormGroup>
          <Button color="primary" className="pull-right" onClick={OnConfirmEditRow}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={() => setEditmsg(false)}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={modalshow}>
        <ModalHeader toggle={() => setmodalshow(false)}>Warning</ModalHeader>
        <ModalBody>
          <div> Do you want to delete this data?</div>
        </ModalBody>
        <ModalFooter>
          <Button color="primary" className="pull-right" onClick={deleteGenre}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={() => setmodalshow(false)}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
    </div>
  );
};

export default VideoGenre;
