import React, { useState, useEffect } from 'react';
import { useTable, useSortBy, usePagination } from 'react-table';
import { Link } from 'react-router-dom';
import { Panel, PanelHeader, PanelBody } from '../../components/panel/panel.jsx';
import { Button, Input, FormGroup, Label, Form, Row, Col, Modal, ModalBody, ModalHeader, ModalFooter } from 'reactstrap';
import { Multiselect } from 'multiselect-react-dropdown';
import { Table } from '../../components/table/reactTable';
import { getData, deleteData, patchData } from '../../services/Utils/DB/DB';
import { notification, Spin } from 'antd';
import { addNotification } from '../../components/ui-notifications';

const Subscribers = () => {
  const [rows, setRows] = useState([]);
  const utoken = localStorage.getItem('utoken') || '';
  const [modalshow, setmodalshow] = useState(false);
  const [Editmsg, setEditmsg] = useState(false);
  const [ID, setID] = useState();
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [user_roles, setUserRoles] = useState([]);
  const [focusAfterClose, setFocusAfterClose] = useState(true);
  const [loading, setloading] = useState(false);
  const [options] = useState([
    { role: 'Subscriber', id: 1 },
    { role: 'Content Approver', id: 3 },
    { role: 'Super Admin', id: 4 },
  ]);

  const onChangeFirstName = (e) => {
    const first = e.target.value;
    setFirstName(first);
  };
  const onChangeLastName = (e) => {
    const last = e.target.value;
    setLastName(last);
  };
  const onChangeEmail = (e) => {
    const email = e.target.value;
    setEmail(email);
  };
  const onChangePhone = (e) => {
    const phone = e.target.value;
    setPhone(phone);
  };
  const handleMultiselect = (selectedList, selectedItem) => {
    const updatedRoles = [...user_roles, selectedItem.role];
    setUserRoles(updatedRoles);
  };

  const handleRemoval = (selectedList, removedItem) => {
    const updatedRole = user_roles.filter((role) => role !== removedItem.role);
    setUserRoles(updatedRole);
  };

  useEffect(() => {
    setloading(!loading);
    getData('/subscribers', utoken)
      .then((data) => {
        if (data) {
          setRows(data);
          setloading(loading);
          console.log(data);
        }
      })
      .catch((error) => {
        setloading(loading);
        console.log(error);
      });
  }, []);

  const columns = React.useMemo(
    () => [
      {
        Header: 'Name',
        columns: [
          {
            Header: 'First Name',
            accessor: 'displayName',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Email',
        columns: [
          {
            Header: 'Email',
            accessor: 'email',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Phone Number',
        columns: [
          {
            Header: 'Phone Number',
            accessor: 'phoneNumber',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Status',
        columns: [
          {
            Header: 'Status',
            accessor: 'age',
            sortable: true,
            Cell: ({ row }) => {
              return (
                <span className="edit">
                  {row.values.emailVerified ? (
                    <span className="label label-green">Confirmed</span>
                  ) : (
                    <span className="label label-danger">Not Confirmed</span>
                  )}
                </span>
              );
            },
          },
        ],
      },
      {
        Header: 'Roles',
        columns: [
          {
            Header: 'Roles',
            accessor: 'roles',
            sortable: true,
            Cell: ({ row }) => {
              return row.values.roles.map((item, index) => (
                <span key={index} style={{ paddingRight: '10px' }}>
                  <span
                    className={
                      item === 'Subscriber'
                        ? 'label label-warning'
                        : item === 'super-admin'
                        ? 'label label-danger'
                        : item === 'content-creator'
                        ? 'label label-pink'
                        : item === 'content-approver'
                        ? 'label label-primary'
                        : null
                    }
                  >
                    {item.toUpperCase()}
                  </span>
                </span>
              ));
            },
          },
        ],
      },
      {
        Header: 'Edit',
        columns: [
          {
            Header: '',
            accessor: 'edit',
            sortable: false,
            Cell: ({ row }) => {
              return (
                <span className="edit">
                  <a className="btn btn-primary btn-icon btn-circle btn-sm">
                    <i className="fas fa-pencil-alt" onClick={() => OnEditRow(row.index)}></i>
                  </a>
                  <a className="btn btn-danger btn-icon btn-circle btn-sm">
                    <i className="fas fa-trash-alt" onClick={() => OnDeleteRow(row.index)}></i>
                  </a>
                </span>
              );
            },
          },
        ],
      },
    ],
    []
  );

  const OnDeleteRow = (id) => {
    setID(id);
    setmodalshow(true); 
  };

  const OnConfirmDeleteRow = () => {
    setloading(true);
    deleteData(`/subscribers?uid=${rows[ID].uid}`, utoken)
      .then((data) => {
        if (data) {
          setRows([...rows.filter((item, index) => item !== rows[ID])]);
          console.log(data);
          addNotification('danger', 'DELETED', 'Subscriber successfully deleted from the Subscribers list.', 'bottom-right');
        }
        setloading(false);
      })
      .catch((error) => {
        addNotification('danger', 'Error', error, 'bottom-right');
        setloading(false);
        console.log(error);
      });
    setmodalshow(false);
  };

  const OnEditRow = (id) => {
    setID(id); 
    setUserRoles([]);
    setEditmsg(true);
  };

  const OnConfirmEditRow = (e) => {
    e.preventDefault();
    if ((firstName === '') | (lastName === '')) {
      return notification.warn({
        message: <h1>Notification</h1>,
        description: `warn: complete all the fields to submit form.`,
        placement: 'bottomRight',
      });
    }
    setloading(true);
    var form_data = {
      firstName: firstName,
      lastName: lastName,
      // email: email,
      // phoneNumber: phone,
      roles: user_roles,
      uid: rows[ID].uid,
    };
    // console.log(rows[ID]);
    // console.log(utoken);
    patchData(`/subscribers`, form_data, utoken)
      .then(({ data }) => {
        if (data) {
          let clone = [...rows];
          clone[ID] = data;
          setRows(clone);
          console.log(data);
          setloading(false);
          addNotification('warning', 'EDITED', 'Subscriber info successfully edited.', 'bottom-right');
        }
      })
      .catch((error) => {
        setloading(false);
        addNotification('danger', 'Error', error, 'bottom-right');
      });
    setEditmsg(false);
  };
  return (
    <div>
      <ol className="breadcrumb float-xl-right">
        <li className="breadcrumb-item">
          <Link to="/">Dashboard</Link>
        </li>
        <li className="breadcrumb-item">
          <Link to="/subscriber/view-subscribers">Subscribers</Link>
        </li>
        <li className="breadcrumb-item active">View Subscribers</li>
      </ol>
      <h1 className="page-header">
        Subscribers <small>manage and approve the subscribers here.</small>
      </h1>
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={Editmsg}>
        <ModalHeader toggle={() => setEditmsg(false)}>Edit Subscriber Form</ModalHeader>
        <ModalFooter>
          <Row style={{ width: '100%' }}>
            <Col>
              <Form>
                <Row>
                  <Col>
                    <FormGroup>
                      <Label for="first">First Name</Label>
                      <Input
                        type="text"
                        name="first"
                        id="first"
                        // placeholder="Product Name"
                        value={firstName}
                        onChange={onChangeFirstName}
                        // className={!isInvalidName ? "" : "is-invalid"}
                      />
                    </FormGroup>
                  </Col>
                  <Col>
                    <FormGroup>
                      <Label for="last">Last Name</Label>
                      <Input
                        type="text"
                        name="last"
                        id="last"
                        // placeholder="Product Name"
                        value={lastName}
                        onChange={onChangeLastName}
                        // className={!isInvalidName ? "" : "is-invalid"}
                      />
                    </FormGroup>
                  </Col>
                </Row>
                {/* <Row>
                  <Col md={8}>
                    <FormGroup>
                      <Label for="email">Email</Label>
                      <Input
                        type="email"
                        name="email"
                        id="email"
                        // placeholder="Product Name"
                        value={email}
                        onChange={onChangeEmail}
                        // className={!isInvalidName ? "" : "is-invalid"}
                      />
                    </FormGroup>
                  </Col>
                  <Col>
                    <FormGroup>
                      <Label for="phone">Phone No.</Label>
                      <InputMask
                        id="phone"
                        mask="+\91 999 999 9999"
                        maskChar="_"
                        className="form-control"
                        value={phone}
                        onChange={onChangePhone}
                      ></InputMask>
                    </FormGroup>
                  </Col>
                </Row> */}
                <Row>
                  <Col>
                    <FormGroup>
                      <Label for="role">Subscriber Roles</Label>
                      <Multiselect
                        id="role"
                        onRemove={handleRemoval}
                        onSelect={handleMultiselect}
                        options={options}
                        displayValue={'role'}
                      />
                    </FormGroup>
                  </Col>
                </Row>
              </Form>
              <div className="card-footer">
                <Row>
                  <Col>
                    <div className="pull-right">
                      <button onClick={OnConfirmEditRow} className="btn btn-primary mr-3">
                        Submit
                      </button>
                      <button onClick={() => setEditmsg(false)} className="btn btn-secondary">
                        Cancel
                      </button>
                    </div>
                  </Col>
                </Row>
              </div>
            </Col>
          </Row>
        </ModalFooter>
      </Modal>

      <Modal returnFocusAfterClose={focusAfterClose} isOpen={modalshow}>
        <ModalHeader toggle={() => setmodalshow(false)}>Warning</ModalHeader>
        <ModalBody>
          <div> Do you want to delete this data?</div>
        </ModalBody>
        <ModalFooter>
          <Button color="primary" className="pull-right" onClick={OnConfirmDeleteRow}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={() => setmodalshow(false)}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>

      <Panel>
        <PanelHeader>All Subscribers Lists</PanelHeader>
        <div className="table-responsive">
          <Table columns={columns} data={rows} />
        </div>
      </Panel>
    </div>
  );
};

export default Subscribers;
