import React, { useState, useEffect } from 'react';
import { useTable, useSortBy, usePagination } from 'react-table';
import { Button, Modal, ModalBody, ModalFooter, Label, Input, FormGroup, Form, ModalHeader } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Panel, PanelHeader, PanelBody } from '../../components/panel/panel.jsx';
import makeData from '../make-data';
import { getData, postData, deleteData, patchData } from '../../services/Utils/DB/DB';
import { Image } from 'antd';
import { EyeFilled } from '@ant-design/icons';
import { useHistory } from 'react-router-dom';
import { notification } from 'antd';

const LiveVideos = () => {
  const [rows, setRows] = useState([]);
  const utoken = localStorage.getItem('utoken') || '';
  const [modalshow, setmodalshow] = useState(false);
  const [Editmsg, setEditmsg] = useState(false);
  const [loading, setloading] = useState(false);
  const [reloadStatus, setReloadStatus] = useState(0);
  const [focusAfterClose, setFocusAfterClose] = useState(true);
  const [ID, setID] = useState();
  const history = useHistory();
  useEffect(() => {
    setloading(true);
    getData('/live', utoken)
      .then((data) => {
        if (data) {
          setloading(false);
          setRows(data.data);
          console.log(data.data);
        }
      })
      .catch((error) => {
        setloading(false);
        console.log(error);
      });
  }, []);

  const columns = React.useMemo(
    () => [
      {
        Header: 'Name',
        columns: [
          {
            Header: 'Title',
            accessor: 'titleName',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Name',
        columns: [
          {
            Header: 'Live Url',
            accessor: 'liveUrl',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Genre',
        columns: [
          {
            Header: 'Genre',
            accessor: 'genre',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Content Creator',
        columns: [
          {
            Header: 'Content Creator',
            accessor: 'contentCreator',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Date Of Creation',
        columns: [
          {
            Header: 'Date Of Creation',
            accessor: 'dateCreated',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Live Date',
        columns: [
          {
            Header: 'Live Date',
            accessor: 'liveDate',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Media',
        columns: [
          {
            Header: 'Media',
            // accessor: 'edit',
            sortable: false,
          },
        ],
      },
      {
        Header: 'Status',
        columns: [
          {
            Header: 'Status',
            accessor: 'age',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Edit',
        columns: [
          {
            Header: '',
            accessor: 'edit',
            sortable: false,
          },
        ],
      },
    ],
    []
  );

  const data = React.useMemo(() => makeData(2000), []);

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    page,

    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,
    state: { pageIndex, pageSize },
  } = useTable({ columns, data, initialState: { pageIndex: 2 } }, useSortBy, usePagination);

  const OnSelectRow = (id) => {
    localStorage.setItem('SelectedLiveVideoData', JSON.stringify(rows[id]));
    history.push('/content-management/live-video/details');
  };
  const OnEditRow = (id) => {
    history.push(`/content-management/live-video/edit/${id}`)
  }
  const OnDeleteRow = (id) => {
    setID(id);
    console.log(rows[id]);
    setmodalshow(true);
  };

  const OnDeleteConfirm = () => {
    const token = localStorage.getItem('utoken');
    setloading(true);
    // console.log(rows[ID].genreId);
    deleteData(`/live?liveId=${rows[ID].liveId}`, token)
      .then((response) => {
        setRows([...rows.filter((item, index) => item !== rows[ID])]);
        setloading(false);
        notification.success({
          message: <h1>Notification</h1>,
          description: `success: Live Video successfully removed from the video list.`,
          placement: 'bottomRight',
        });
        setReloadStatus((prev) => prev + 1);
      })
      .catch((error) => {
        setloading(false);
        notification.error({
          message: <h1>error</h1>,
          description: `${error}`,
          placement: 'bottomRight',
        });
        setReloadStatus((prev) => prev + 1);
      });
    setmodalshow(false);
  };
  return (
    <div>
      <ol className="breadcrumb float-xl-right">
        <li className="breadcrumb-item">
          <Link to="/">Dashboard</Link>
        </li>
        <li className="breadcrumb-item">
          <Link to="/content-management/live-video/view">Videos</Link>
        </li>
        <li className="breadcrumb-item active">Live Videos</li>
      </ol>
      <h1 className="page-header">
        Live Video's <small>manage and approve the live videos here.</small>
      </h1>
      <Panel>
        <PanelHeader>All Live Videos Lists</PanelHeader>
        <div className="table-responsive">
          <table className="table table-striped table-bordered" {...getTableProps()}>
            <thead>
              {headerGroups.map((headerGroup) => (
                <tr {...headerGroup.getHeaderGroupProps()}>
                  {headerGroup.headers.map((column) => (
                    <th {...column.getHeaderProps(column.getSortByToggleProps())}>
                      <div className="d-flex" style={{ minWidth: '1%' }}>
                        <span>{column.render('Header')}</span>
                        <span className="ml-auto">
                          {column.sortable ? (
                            column.isSorted ? (
                              column.isSortedDesc ? (
                                <i className="fa fa-sort-down fa-fw f-s-14 text-blue"></i>
                              ) : (
                                <i className="fa fa-sort-up fa-fw f-s-14 text-blue"></i>
                              )
                            ) : (
                              <i className="fa fa-sort fa-fw f-s-14 opacity-3"></i>
                            )
                          ) : (
                            ''
                          )}
                        </span>
                      </div>
                    </th>
                  ))}
                </tr>
              ))}
            </thead>
            {
              // loading?
              // <span className="spinner-small"></span>:
              rows && (
                <tbody {...getTableBodyProps()}>
                  {Object.keys(rows).length <= 0 ? (
                    <tr>
                      <td colSpan={7} style={{ textAlign: 'center', fontWeight: 'bold', fontSize: '26px' }}>
                        <span>No Live Video is available.</span>
                      </td>
                    </tr>
                  ) : (
                    Object.keys(rows).map((id) => {
                      return (
                        <tr key={rows[id].displayName}>
                          {/* {console.log(rows[id])} */}
                          <td>{rows[id].title}</td>
                          <td>{rows[id].liveUrl}</td>
                          <td>{rows[id].genre}</td>
                          <td>{rows[id].createdBy}</td>
                          <td>{rows[id].createdAt}</td>
                          <td>{rows[id].liveDate}</td>
                          <td style={{ display: 'flex', justifyContent: 'space-between', width: '200px' }}>
                            <Image src={rows[id].imageUrls.portrait} alt="" style={{ width: '80px', height: '50px' }} />
                          </td>
                          <td>
                            {console.log(rows[id])}
                            {rows[id].status === 'ENABLED' ? (
                              <span className="label label-green">Published</span>
                            ) : (
                              <span className="label label-danger">Pending</span>
                            )}
                          </td>
                          <td className="edit">
                            <EyeFilled style={{ fontSize: '24px' }} onClick={() => OnSelectRow(id)} />
                            <a className="btn btn-primary btn-icon btn-circle btn-sm">
                              <i className="fas fa-pencil-alt" onClick={() => OnEditRow(id)}></i>
                            </a>
                            <a className="btn btn-danger btn-icon btn-circle btn-sm">
                              <i className="fas fa-trash-alt" onClick={() => OnDeleteRow(id)}></i>
                            </a>
                          </td>
                        </tr>
                      );
                    })
                  )}
                </tbody>
              )
            }
          </table>
        </div>
        <hr className="m-0" />
        {Object.keys(rows).length <= 0 ? null : (
          <PanelBody>
            <div className="d-flex align-items-center justify-content-center">
              <ul className="pagination mb-0">
                <li className="page-item">
                  <button className="page-link" onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
                    <i className="fa fa-angle-double-left"></i>
                  </button>
                </li>
                <li className="page-item">
                  <button className="page-link" onClick={() => previousPage()} disabled={!canPreviousPage}>
                    <i className="fa fa-angle-left"></i>
                  </button>
                </li>
                <li className="page-item d-flex align-items-center px-2">
                  <div>
                    Page
                    <strong>
                      {pageIndex + 1} of {pageOptions.length}
                    </strong>
                  </div>
                </li>
                <li className="page-item">
                  <button className="page-link" onClick={() => nextPage()} disabled={!canNextPage}>
                    <i className="fa fa-angle-right"></i>
                  </button>
                </li>
                <li className="page-item">
                  <button className="page-link" onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
                    <i className="fa fa-angle-double-right"></i>
                  </button>
                </li>
              </ul>
              <div className="ml-3 mr-1">Go to page:</div>
              <div className="width-50 mx-2">
                <input
                  className="form-control"
                  type="number"
                  defaultValue={pageIndex + 1}
                  onChange={(e) => {
                    const page = e.target.value ? Number(e.target.value) - 1 : 0;
                    gotoPage(page);
                  }}
                />
              </div>
              <div>
                <select
                  className="form-control"
                  value={pageSize}
                  onChange={(e) => {
                    setPageSize(Number(e.target.value));
                  }}
                >
                  {[10, 20, 30, 40, 50].map((pageSize) => (
                    <option key={pageSize} value={pageSize}>
                      Show {pageSize}
                    </option>
                  ))}
                </select>
              </div>
            </div>
          </PanelBody>
        )}
      </Panel>
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={modalshow}>
        <ModalHeader toggle={() => setmodalshow(false)}>Warning</ModalHeader>
        <ModalBody>
          <div> Do you want to delete this data?</div>
        </ModalBody>
        <ModalFooter>
          <Button color="primary" className="pull-right" onClick={OnDeleteConfirm}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={() => setmodalshow(false)}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
    </div>
  );
};

export default LiveVideos;
