import React, { useState, useEffect } from 'react';
import { Button, Modal, ModalBody, ModalFooter, Label, Input, FormGroup, Form, ModalHeader } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Panel, PanelHeader } from './../../components/panel/panel.jsx';
import { getData, postData, deleteData, patchData } from './../../services/Utils/DB/DB';
import { notification } from 'antd';
import { Table } from '../../components/table/reactTable';
import { addNotification } from '../../components/ui-notifications';
const ManageCategories = () => {
  const [rows, setRows] = useState([]);
  const [reloadStatus, setReloadStatus] = useState(0);
  const [modalshow, setmodalshow] = useState(false);
  const [Editmsg, setEditmsg] = useState(false);
  const [ID, setID] = useState();
  const [loading, setloading] = useState(false);

  const utoken = localStorage.getItem('utoken') || '';
  useEffect(() => {
    setloading(true);
    getData('/categories', utoken)
      .then((data) => {
        if (data) {
          const Arr = [];
          Object.values(data).map((i) => {
            const item = {
              categoryId: i.categoryId,
              category: i.category,
              contentCreator: i.createdBy,
              dateCreated: i.createdAt,
              langId: i.langId,
            };
            Arr.push(item);
          });
          setRows(Arr);
          setloading(false);
          console.log('Manage', data);
        }
      })
      .catch((error) => {
        console.log(error);
        setloading(false);
      });
  }, [reloadStatus]);

  const [category, setCategory] = useState('');
  const onChangeCategory = (e) => {
    const category = e.target.value;
    setCategory(category);
  };
  const [displayOrder, setDisplayOrder] = useState('');
  const onChangeDisplayOrder = (e) => {
    const displayOrder = e.target.value;
    setDisplayOrder(displayOrder);
  };
  const addCategory = (e) => {
    e.preventDefault();
    setloading(true);
    const token = localStorage.getItem('utoken');
    const form_data = {
      iconType: '',
      category: category,
      displayOrder: displayOrder,
    };
    postData('/categories', JSON.stringify(form_data), token)
      .then((response) => {
        setloading(false);
        addNotification('success', 'ADDED', 'Category successfully added to the category list.', 'bottom-right');
        setReloadStatus((prev) => prev + 1);
      })
      .catch((error) => {
        setloading(false);
        addNotification('danger', 'Error', error, 'bottom-right');
      });
    toggle();
  };

  const OnDeleteRow = (id) => {
    setID(id);
    setmodalshow(true);
  };

  const deleteCategory = (e, id) => {
    e.preventDefault();
    setloading(true);
    const token = localStorage.getItem('utoken');
    deleteData(`/categories?categoryId=${rows[ID].categoryId}`, token)
      .then((response) => {
        setloading(false);
        addNotification('danger', 'Deleted', 'Category successfully removed from the category list.', 'bottom-right');
        setReloadStatus((prev) => prev + 1);
      })
      .catch((error) => {
        setloading(false);
        addNotification('danger', 'Error', error, 'bottom-right');
      });
    setmodalshow(false);
  };

  const OnEditRow = async (id) => {
    setID(id);
    setEditmsg(true);
  };

  const OnConfirmEditRow = (e) => {
    e.preventDefault();
    if (category === '') {
      return notification.warn({
        message: <h1>Notification</h1>,
        description: `warn: complete all the fields to submit form.`,
        placement: 'bottomRight',
      });
    }
    setloading(true);
    const token = localStorage.getItem('utoken');
    var form_data = {
      category: category,
      displayOrder: displayOrder,
      categoryId: rows[ID].categoryId,
    }; 
    patchData(`/categories`, form_data, token)
      .then(({ data }) => {
        if (data) {
          setloading(false);
          let clone = [...rows];
          clone[ID] = data;
          setRows(clone);
          console.log(data);
          addNotification('warning', 'EDITED', 'Category info successfully edited.', 'bottom-right');
        }
      })
      .catch((error) => {
        setloading(false);
        addNotification('danger', 'EDITED', error, 'bottom-right');
        console.log(error);
      });
    setEditmsg(false);
  };

  const columns = React.useMemo(
    () => [
      {
        Header: 'Category',
        columns: [
          {
            Header: 'Category',
            accessor: 'category',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Content Creator',
        columns: [
          {
            Header: 'Content Creator',
            accessor: 'contentCreator',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Date Of Creation',
        columns: [
          {
            Header: 'Date Of Creation',
            accessor: 'dateCreated',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Edit',
        columns: [
          {
            Header: '',
            accessor: 'edit',
            sortable: false,
            Cell: ({ row }) => {
              return (
                <span className="edit">
                  {/* <a className="btn btn-primary btn-icon btn-circle btn-sm">
                    <i className="fas fa-pencil-alt" onClick={() => OnEditRow(row.index)}></i>
                  </a> */}
                  <a className="btn btn-danger btn-icon btn-circle btn-sm">
                    <i className="fas fa-trash-alt" onClick={() => OnDeleteRow(row.index)}></i>
                  </a>
                </span>
              );
            },
          },
        ],
      },
    ],
    []
  );

  const [open, setOpen] = useState(false);
  const [focusAfterClose, setFocusAfterClose] = useState(true);

  const toggle = () => setOpen(!open);

  return (
    <div>
      <ol className="breadcrumb float-xl-right">
        <li className="breadcrumb-item">
          <Link to="/">Dashboard</Link>
        </li>
        <li className="breadcrumb-item">
          <Link to="/content-management/video-category">Video Categories</Link>
        </li>
        <li className="breadcrumb-item active">View Categories</li>
      </ol>
      <h1 className="page-header">
        Video Categories <small>manage and edit the video categories here.</small>
      </h1>
      <Panel>
        <PanelHeader noButton={true}>
          All Video Categories Lists
          <Button color="default" size="xs" className="mr-2 rounded-0 pull-right" onClick={toggle}>
            Add Category
          </Button>
        </PanelHeader>
        <div className="table-responsive">
          <Table columns={columns} data={rows} />
        </div>
      </Panel>
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={Editmsg}>
        <ModalHeader onClick={() => setEditmsg(false)}>Edit Category</ModalHeader>
        <ModalFooter>
          <FormGroup className="w-100">
            <Label for="category">Category Title</Label>
            <Input type="text" name="category" id="category" value={category} onChange={onChangeCategory} />
          </FormGroup>
          <FormGroup className="w-100">
            <Label for="displayOrder">Sort Order</Label>
            <Input type="text" name="displayOrder" id="displayOrder" value={displayOrder} onChange={onChangeDisplayOrder} />
          </FormGroup>
          <Button color="primary" className="pull-right" onClick={OnConfirmEditRow}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={() => setEditmsg(false)}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={open}>
        <ModalHeader toggle={toggle}>Add Category</ModalHeader>
        <ModalFooter>
          <FormGroup className="w-100">
            <Label for="category">Category Title</Label>
            <Input type="text" name="category" id="category" value={category} onChange={onChangeCategory} />
          </FormGroup>
          <FormGroup className="w-100">
            <Label for="displayOrder">Sort Order</Label>
            <Input type="text" name="displayOrder" id="displayOrder" value={displayOrder} onChange={onChangeDisplayOrder} />
          </FormGroup>
          <Button color="primary" className="pull-right" onClick={addCategory}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={toggle}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={modalshow}>
        <ModalHeader toggle={() => setmodalshow(false)}>Warning</ModalHeader>
        <ModalBody>
          <div> Do you want to delete this data?</div>
        </ModalBody>
        <ModalFooter>
          <Button color="primary" className="pull-right" onClick={deleteCategory}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={() => setmodalshow(false)}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
    </div>
  );
};

export default ManageCategories;
