import React, { useState, useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { patchData, postData } from '../../services/Utils/DB/DB';
import { addNotification } from '../../components/ui-notifications';

export const SingleVideoScreen = () => {
  const [vDetail, setVDetail] = useState(JSON.parse(localStorage.getItem('SelectedVideoData')));
  const [activeLink, setactiveLink] = useState({
    Original: true,
    HLS: false,
  });

  const OriginalItem = () => {
    const clone = { ...activeLink };
    clone.Original = true;
    clone.HLS = false;
    setactiveLink(clone);
    console.log(clone);
  };

  const HLSItem = () => {
    const clone = { ...activeLink };
    clone.Original = false;
    clone.HLS = true;
    setactiveLink(clone);
  };
  console.log(vDetail);
  const OnConvertVideo = (e) => {
    e.preventDefault();
    const token = localStorage.getItem('utoken');

    const form_data = {
      id: vDetail.singleId,
      type: 'single',
      videoS3Key: vDetail.videoS3Keys.original,
    };

    console.log(form_data);
    postData('/mediaconvert', JSON.stringify(form_data), token)
      .then((response) => {
        addNotification('success', 'CONVERTED', 'Video successfully converted to HLS.', 'bottom-right');
      })
      .catch((error) => {
        addNotification('danger', 'Error', error, 'bottom-right');
      });
  };

  const onPublishVideo = async () => {
    console.log('called: onPublishVideo');

    const token = await localStorage.getItem('utoken');

    const form_data = {
      title: vDetail.title,
      category: vDetail.category,
      language: vDetail.language,
      synopsis: vDetail.synopsis,
      releaseDate: vDetail.releaseDate,
      genre: vDetail.genre,
      castCrew: vDetail.castCrew,
      duration: '',
      singleId: vDetail.singleId,
      paymentType: vDetail.paymentType,
      maturityRating: vDetail.maturityRating,
      activeImage: 'landscape',
      subscriptionTypes: [''],
      rentalPrices: [],
      images: [
        {
          imageExt: 'jpg',
          imageType: 'landscape',
        },
        {
          imageExt: 'jpg',
          imageType: 'portrait',
        },
      ],
      status: 'PUBLISHED',
    };

    console.log(form_data);
    patchData('/singles', JSON.stringify(form_data), token)
      .then(({ data }) => {
        console.log(data);
        setVDetail(data);
      })
      .catch((error) => console.log(error));
  };
  
  const onUnPublishVideo = async () => {
    console.log('called:onUnPublishVideo');

    const token = await localStorage.getItem('utoken');

    const form_data = {
      ...vDetail,
      images: [
        {
          imageExt: 'jpg',
          imageType: 'landscape',
        },
        {
          imageExt: 'jpg',
          imageType: 'portrait',
        },
      ],
      status: 'DISABLED',
    };

    console.log(form_data);
    patchData('/singles', JSON.stringify(form_data), token)
      .then(({ data }) => {
        setVDetail(data);
      })
      .catch((error) => console.log(error));
  };

  return (
    <div>
      <ol className="breadcrumb float-xl-right">
        <li className="breadcrumb-item">
          <Link to="/">Home</Link>
        </li>
        <li className="breadcrumb-item">
          <Link to="/content-management/single-video/view">View Single Videos</Link>
        </li>
        <li className="breadcrumb-item active">View Video Details</li>
      </ol>
      <h1 className="page-header">
        View Single Video Details<small> View single video detail here.</small>
      </h1>
      <ul className="nav nav-tabs nav-tabs-inverse">
        <li className="nav-item">
          <a className={activeLink.Original ? 'nav-link active' : 'nav-link'} onClick={OriginalItem}>
            Original Video
          </a>
        </li>
        {vDetail.status !== 'PENDING' ? (
          <li className="nav-item">
            <a className={activeLink.HLS ? 'nav-link active' : 'nav-link'} onClick={HLSItem}>
              HLS Video
            </a>
          </li>
        ) : null}
        <li className="nav-item ml-auto">
          <div className="tab-action" style={{ marginTop: '5px' }}>
            {vDetail.status == 'CONVERSION_COMPLETE' || vDetail.status == 'DISABLED' ? (
              <>
                <span className="label label-lime mr-2 rounded-0 ">Converted</span>
                <button type="button" className="mr-2 rounded-0 pull-right btn btn-success btn-xs" onClick={onPublishVideo}>
                  Publish Now 
                </button>

                <button type="button" className="mr-2 rounded-0 pull-right btn btn-default btn-xs">
                  Add Banner
                </button>
              </>
            ) : vDetail.status == 'PENDING' ? (
              <>
                <span className="label label-secondary mr-2 rounded-0 ">{vDetail.status}</span>
                <button type="button" className="mr-2 rounded-0 pull-right btn btn-warning btn-xs" onClick={OnConvertVideo}>
                  CONVERT to HLS 
                  
                </button>

              </>
            ) : vDetail.status == 'CONVERSION_IN_PROGRESS' ? (
              <>
                <span className="label label-secondary mr-2 rounded-0 ">CONVERSION PROGRESS</span>
                <button type="button" className="mr-2 rounded-0 pull-right btn btn-warning btn-xs disabled">
                  CONVERT to HLS <i class="fa fa-spinner fa-spin" aria-hidden="true"></i> 
                </button>

              </>
            ) : vDetail.status == 'PUBLISHED' ? (
              <>
                <span className="label label-lime mr-2 rounded-0 ">{vDetail.status}</span>
                <button type="button" className="mr-2 rounded-0 pull-right btn btn-danger btn-xs" onClick={onUnPublishVideo}>
                  Disable Now
                </button>

                <button type="button" className="mr-2 rounded-0 pull-right btn btn-default btn-xs">
                  Add Banner
                </button>
              </>
            ) : (
              <>
                <span className="label label-lime mr-2 rounded-0 ">{vDetail.status}</span>
                <button type="button" className="mr-2 rounded-0 pull-right btn btn-success btn-xs" onClick={onPublishVideo}>
                  Publish Now
                </button>

                <button type="button" className="mr-2 rounded-0 pull-right btn btn-default btn-xs">
                  Add Banner
                </button>
              </>
            )}
          </div>
        </li>
      </ul>
      <div>
      {activeLink.Original ? (
              <video width="100%" height="auto" controls>
                <source src={vDetail.videoUrls.original} type="video/mp4" />
                
              </video>
            ) : (
              <video width="100%" height="auto" controls>
                <source src={vDetail.videoUrls.hls} type="video/mp4" />
                
              </video>
            )}
      </div> 

      <div class="mt-4">
        <div class="row">          
          <div class="col-5">
            <div class="table-responsive">
              <table class="table table-sm table-detail-video">
                <tbody>
                  <tr style={{ width: '100px' }}>
                    <th colspan="2">
                      <h2>{vDetail.title}</h2>
                    </th>
                  </tr>
                  <tr>
                    <td colspan="2">
                      <hr style={{ margin: '0px' }} />
                    </td>
                  </tr>
                  {JSON.parse(vDetail.castCrew).map((item, index) => {
                    return (
                      <tr key={index}>
                        <td style={{ width: '100px' }}>
                          <strong>{item.title}:</strong>
                        </td>
                        <td style={{ margin: '0px' }}>{item.name}</td>
                      </tr>
                    );
                  })}

                  <tr>
                    <td colspan="2">
                      <hr style={{ margin: '0px' }} />
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <strong>Category:</strong>
                    </td>
                    <td>{vDetail.category}</td>
                  </tr>
                  <tr>
                    <td>
                      <strong>Genre:</strong>
                    </td>
                    <td>{vDetail.genre}</td>
                  </tr>
                  <tr>
                    <td>
                      <strong>Language:</strong>
                    </td>
                    <td>{vDetail.language}</td>
                  </tr>
                  <tr>
                    <td>
                      <strong>Date:</strong>
                    </td>
                    <td>{vDetail.createdAt}</td>
                  </tr>
                  <tr>
                    <td colspan="2">
                      <hr style={{ margin: '0px' }} />
                    </td>
                  </tr>
                  <tr>
                    <td colSpan="2">
                      <div className="pb-1"><strong>Synopsis:</strong></div>
                      <p>{vDetail.synopsis}</p>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>            
          </div>

          <div class="col-7">
            
            <div className="detail-images">
              <div className="row">
                <div className="col-md-5">
                  <h6>Landscape</h6>
                  <img class="w-100" src={vDetail.imageUrls.landscape} alt="Landscape" />
                </div>  
                <div className="col-md-2">
                  <h6>Portrait</h6>
                  <img class="w-100" src={vDetail.imageUrls.portrait} alt="Portrait" />  
                </div>
                <div className="col-md-5">
                  <h6>Background</h6>
                  <img class="w-100" src={vDetail.imageUrls.background} alt="Background" />  
                </div>   
              </div>           
            </div>
            <div className="detail-images">
              <div className="row">
                <div className="col-md-5">
                  <h6>Mobile Landscape</h6>
                  <img class="w-100" src={vDetail.imageUrls.landscape} alt="Mobile Landscape" />
                </div>  
                <div className="col-md-2">
                  <h6>Mobile Portrait</h6>
                  <img class="w-100" src={vDetail.imageUrls.portrait} alt="Mobile Portrait" />  
                </div>
                <div className="col-md-5">
                  <h6>Mobile Background</h6>
                  <img class="w-100" src={vDetail.imageUrls.background} alt="Mobile Background" />  
                </div>  
              </div>         
            </div>
            <div className="detail-images">
              <div className="row">
                <div className="col-md-5">
                  <h6>TV Landscape</h6>
                  <img class="w-100" src={vDetail.imageUrls.landscape} alt="TV Landscape" />
                </div>  
                <div className="col-md-2">
                  <h6>TV Portrait</h6>
                  <img class="w-100" src={vDetail.imageUrls.portrait} alt="TV Portrait" />  
                </div>
                <div className="col-md-5">
                  <h6>TV Background</h6>
                  <img class="w-100" src={vDetail.imageUrls.background} alt="TV Background" />  
                </div>  
              </div>         
            </div>
            
            
                  
                
           
          </div>

        </div>
      </div>
      
      
    </div>
  );
};
