import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { Panel, PanelBody, PanelFooter, PanelHeader } from '../../components/panel/panel';
import { Button, Input, FormGroup, Label, Form, Row, Col } from 'reactstrap';
import { postData } from '../../services/Utils/DB/DB';
import { notification } from 'antd';
import { useHistory } from 'react-router-dom';
import axios from "axios";
export const AddRentalPackage = () => {
  const [Name, setName] = useState('');
  const [Duration, setDuration] = useState('');
  const [Price, setPrice] = useState('');
  const history = useHistory();

  const onChangeName = (e) => {
    const Name = e.target.value;
    setName(Name);
  };

  const onChangePrice = (e) => {
    const Price = e.target.value;
    setPrice(Price);
  };

  const onChangeDuration = (e) => {
    const Duration = e.target.value;
    setDuration(Duration);
  };

  const AddRentalPackage = (e) => {
    e.preventDefault();
    if ((Name === '') | (Price === '') | (Duration === '')) {
      return notification.warn({
        message: <h1>Notification</h1>,
        description: `warn: complete all the fields to submit form.`,
        placement: 'bottomRight',
      });
    }
    const token = localStorage.getItem('utoken');

    const form_data = {
      rentalTypeDisplayName: Name,
      defaultPrice: Price,
      rentalType: Duration,
    };

    postData('/rentaltypes',JSON.stringify(form_data), token)
      .then((response) => {
        notification.success({
          message: <h1>Notification</h1>,
          description: `success: ${Name}  successfully added to the rental packages list.`,
          placement: 'bottomRight',
        });
        history.push('/rental-packages/view-rental-packages');
      })
      .catch((error) => {
        notification.error({
          message: <h1 style={{ color: 'red' }}>Error</h1>,
          description: `${error}.`,
          placement: 'bottomRight',
        });
      });
  };

  return (
    <div>
      <ol className="breadcrumb float-xl-right">
        <li className="breadcrumb-item">
          <Link to="/">Home</Link>
        </li>
        <li className="breadcrumb-item">
          <Link to="/rental-packages/view-rental-packages">Rental Packages</Link>
        </li>
        <li className="breadcrumb-item active">Add Rental Packages</li>
      </ol>
      <h1 className="page-header">
        Add Rental Package <small>add a new Rental Packages here.</small>
      </h1>

      <Panel>
        <PanelHeader noButton={true}>Add Rental Packages Form</PanelHeader>
        <PanelBody>
          <Row>
            <Col md="8">
              <div className="card">
                <div className="card-body">
                  <Form>
                    <Row>
                      <Col>
                        <FormGroup>
                          <Label for="name">Name</Label>
                          <Input type="text" name="name" id="name" value={Name} onChange={onChangeName} />
                        </FormGroup>
                      </Col>
                    </Row>
                    <Row>
                      <Col>
                        <FormGroup>
                          <Label for="price">Price</Label>
                          <Input type="text" name="price" id="price" value={Price} onChange={onChangePrice} />
                        </FormGroup>
                      </Col>
                    </Row>
                    <Row>
                      <Col>
                        <FormGroup>
                          <Label for="price">Duration</Label>
                          <Input type="text" name="price" id="price" value={Duration} onChange={onChangeDuration} />
                        </FormGroup>
                      </Col>
                    </Row>
                  </Form>
                </div>
                <div className="card-footer">
                  <Row>
                    <Col>
                      <div className="pull-right">
                        <button onClick={AddRentalPackage} className="btn btn-primary mr-3">
                          Submit
                        </button>
                        <button
                          className="btn btn-secondary"
                          onClick={() => history.push('/rental-packages/view-rental-packages')}
                        >
                          Cancel
                        </button>
                      </div>
                    </Col>
                  </Row>
                </div>
              </div>
            </Col>
          </Row>
        </PanelBody>
      </Panel>
    </div>
  );
};
