import React from 'react';
import { withRouter } from 'react-router-dom';
import { Alert } from 'reactstrap';
import { PageSettings } from '../../config/page-settings.js';
import { fireDbAuth } from '../../services/firebase';
import { Auth } from '../../services/Utils/Auth/Auth';
import { fetchAdminsList } from '../../services/Utils/DB/DB';
import { Form, Input } from 'antd';

class LoginV1 extends React.Component {
  static contextType = PageSettings;

  constructor(props) {
    super(props);
    this.state = {
      email: '',
      password: '',
      errorTxt: '',
      alert: false,
    };
    this.onDismiss = this.onDismiss.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
    this.context.handleSetPageSidebar(false);
    this.context.handleSetPageHeader(false);
    this.context.handleSetPageContentFullWidth(true);
  }

  componentWillUnmount() {
    this.context.handleSetPageSidebar(true);
    this.context.handleSetPageHeader(true);
    this.context.handleSetPageContentFullWidth(false);
  }
  // username: edwardantonyin@gmail.com
  // password: Y2qUYkmLxi5b

  async handleSubmit(event) {
    const { history } = this.props;
    // event.preventDefault();

    if (!this.state.email || !this.state.password) {
      return alert('All fields are required');
    }

    const auth = new Auth();
    console.log(this.state.email, this.state.password);
    const response = await auth.SignIn(this.state.email, this.state.password);

    if (response === 'success') {
      this.setState({ errorTxt: '', alert: false });
      fireDbAuth.onAuthStateChanged((user) => {
        user.getIdToken(true).then(async (token) => {
          localStorage.setItem('utoken', token);
          history.push('/');
          window.location.reload();
        });
      });
    } else {
      this.setState({ errorTxt: response.message, alert: true });
    }
    // console.log(response);
  }

  handleChange(e) {
    console.log(e.target.id, e.target.value);
    this.setState({
      [e.target.id]: e.target.value,
    });
  }

  onDismiss() {
    this.setState({
      alert: false,
      errorTxt: '',
    });
  }

  render() {
    return (
      <div className="login login-v1">
        <div className="login-container">
          <div className="login-header">
            <div className="brand">
              <span style={{ width: '20px', overflow: 'hidden', marginRight: '15px' }}>
                <img src="/sabha-logo.png" alt="" height="40px" />
              </span>
              <b>Sabha</b> TV
              <small>login with your credentials</small>
            </div>
            <div className="icon">
              <i className="fa fa-lock"></i>
            </div>
          </div>
          <div className="login-body">
            <div className="login-content">
              {/* <form className="margin-bottom-0" onSubmit={this.handleSubmit}> */}
              <Form className="margin-bottom-0" onFinish={this.handleSubmit}>
                <Form.Item
                  name="email"
                  className="form-group m-b-20"
                  hasFeedback
                  rules={[{ required: true, type: 'email', message: 'Please input your email!', validateStatus: 'success' }]}
                >
                  <Input
                    type="email"
                    className="form-control form-control-lg inverse-mode"
                    placeholder="Email Address"
                    required
                    id="email"
                    onChange={this.handleChange}
                  />
                </Form.Item>
                <Form.Item
                  className="form-group m-b-20"
                  name="password"
                  hasFeedback
                  rules={[
                    { required: true, min: 6, message: 'Password must be greater 6 characters', validateStatus: 'success' },
                  ]}
                >
                  <Input
                    type="password"
                    className="form-control form-control-lg inverse-mode"
                    placeholder="Password"
                    required
                    id="password"
                    onChange={this.handleChange}
                  />
                </Form.Item>

                <Alert color="success" isOpen={this.state.alert} toggle={this.onDismiss}>
                  <strong>{this.state.errorTxt}</strong>
                </Alert>
                <div className="checkbox checkbox-css m-b-20">
                  <input type="checkbox" id="remember_checkbox" />
                  <label htmlFor="remember_checkbox" style={{ color: 'grey' }}>
                    Remember Me
                  </label>
                </div>
                <div className="login-buttons">
                  <button type="submit" className="btn btn-success btn-block btn-lg">
                    Sign me in
                  </button>
                </div>
              </Form>
              {/* </form> */}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(LoginV1);
