import React, { useState, useEffect } from 'react';
import { useTable, useSortBy, usePagination } from 'react-table';
import { Link } from 'react-router-dom';
import { Panel, PanelHeader, PanelBody } from '../../components/panel/panel.jsx';
import { Button, Input, FormGroup, Label, Form, Row, Col, Modal, ModalBody, ModalHeader, ModalFooter } from 'reactstrap';
import { Table } from '../../components/table/reactTable';
import { getData, deleteData, patchData } from '../../services/Utils/DB/DB';
import { notification, Spin } from 'antd';

export const RentalPackages = () => {
  const [rows, setRows] = useState([]);
  const [modalshow, setmodalshow] = useState(false);
  const [Editmsg, setEditmsg] = useState(false);
  const [ID, setID] = useState();
  const [focusAfterClose, setFocusAfterClose] = useState(true);
  const [loading, setloading] = useState(false);
  const [Name, setName] = useState('');
  const [Duration, setDuration] = useState('');
  const [Price, setPrice] = useState('');

  const onChangeName = (e) => {
    const Name = e.target.value;
    setName(Name);
  };

  const onChangePrice = (e) => {
    const Price = e.target.value;
    setPrice(Price);
  };
  const onChangeDuration = (e) => {
    const Duration = e.target.value;
    setDuration(Duration);
  };

  const utoken = localStorage.getItem('utoken') || '';

  useEffect(() => {
    setloading(!loading);
    getData('/rentaltypes', utoken)
      .then((data) => {
        if (data) {
          setRows(data);
          setloading(loading);
          console.log(data);
        }
      })
      .catch((error) => {
        setloading(loading);
        console.log(error);
      });
  }, []);

  const columns = React.useMemo(
    () => [
      {
        Header: 'Name',
        columns: [
          {
            Header: 'Name',
            accessor: 'rentalTypeDisplayName',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Price',
        columns: [
          {
            Header: 'Price',
            accessor: 'defaultPrice',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Duration',
        columns: [
          {
            Header: 'Duration',
            accessor: 'rentalType',
            sortable: false,
          },
        ],
      },
      {
        Header: 'Edit',
        columns: [
          {
            Header: '',
            accessor: 'edit',
            sortable: false,
            Cell: ({ row }) => {
              return (
                <span className="edit">
                  <a className="btn btn-danger btn-icon btn-circle btn-sm">
                    <i className="fas fa-trash-alt" onClick={() => OnDeleteRow(row.index)}></i>
                  </a>
                </span>
              );
            },
          },
        ],
      },
    ],
    []
  );

  const OnDeleteRow = (id) => {
    setID(id);
    setmodalshow(true);
    console.log(rows[id].rentalTypeId);
  };

  const OnConfirmDeleteRow = () => {
    setloading(true);
    console.log(rows[ID].rentalTypeId);

    deleteData(`/rentaltypes?rentalTypeId=${rows[ID].rentalTypeId}`, utoken)
      .then((data) => {
        if (data) {
          // setRows(data);
          console.log(data);
          setRows([...rows.filter((item, index) => item !== rows[ID])]);
          console.log(data);
          setloading(false);
          notification.success({
            message: <h1>Notification</h1>,
            description: `success:Admin successfully removed from the admin list.`,
            placement: 'bottomRight',
          });
        }
      })
      .catch((error) => {
        setloading(false);
        notification.error({
          message: <h1>error</h1>,
          description: `${error}`,
          placement: 'bottomRight',
        });
        console.log(error);
      });

    setmodalshow(false);
  };

  const OnEditRow = async (id) => {
    setID(id);
    setEditmsg(true);
  };

  const OnConfirmEditRow = (e) => {
    e.preventDefault();
    if ((Name === '') | (Price === '') | (Duration === '')) {
      return notification.warn({
        message: <h1>Notification</h1>,
        description: `warn: complete all the fields to submit form.`,
        placement: 'bottomRight',
      });
    }
    setloading(true);
    const form_data = {
      rentalTypeDisplayName: Name,
      defaultPrice: Price,
      rentalType: Duration,
    };

    console.log(form_data);
    patchData(`/rentaltypes`, JSON.stringify(form_data), utoken)
      .then(({ data }) => {
        if (data) {
          let clone = [...rows];
          clone[ID] = data;
          setRows(clone);
          console.log(data);
          setloading(false);
          notification.success({
            message: <h1>Notification</h1>,
            description: `success: you successfly edited the rental package info..`,
            placement: 'bottomRight',
          });
        }
      })
      .catch((error) => {
        setloading(false);
        notification.error({
          message: <h1>Error</h1>,
          description: `${error}`,
          placement: 'bottomRight',
        });
      });
    setEditmsg(false);
  };

  return (
    <div>
      <ol className="breadcrumb float-xl-right">
        <li className="breadcrumb-item">
          <Link to="/">Dashboard</Link>
        </li>
        <li className="breadcrumb-item">
          <Link to="/rental-packages/view-rental-packages">Rental Packages</Link>
        </li>
        <li className="breadcrumb-item active">View Rental Packages</li>
      </ol>
      <h1 className="page-header">
        Rental Pages <small>manage and approve the rental pages here.</small>
      </h1>
      <Modal className="modal-lg" returnFocusAfterClose={focusAfterClose} isOpen={Editmsg}>
        <ModalHeader toggle={() => setEditmsg(false)}>Edit Rental Packages Form</ModalHeader>
        <ModalFooter>
          <Row style={{ width: '100%' }}>
            <Col>
              <Form>
                <Row>
                  <Col>
                    <FormGroup>
                      <Label for="name">Name</Label>
                      <Input type="text" name="name" id="name" value={Name} onChange={onChangeName} />
                    </FormGroup>
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <FormGroup>
                      <Label for="price">Price</Label>
                      <Input type="text" name="price" id="price" value={Price} onChange={onChangePrice} />
                    </FormGroup>
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <FormGroup>
                      <Label for="price">Duration</Label>
                      <Input type="text" name="price" id="price" value={Duration} onChange={onChangeDuration} />
                    </FormGroup>
                  </Col>
                </Row>
              </Form>
              <div className="card-footer">
                <Row>
                  <Col>
                    <div className="pull-right">
                      <button onClick={OnConfirmEditRow} className="btn btn-primary mr-3">
                        Submit
                      </button>
                      <button onClick={() => setEditmsg(false)} className="btn btn-secondary">
                        Cancel
                      </button>
                    </div>
                  </Col>
                </Row>
              </div>
            </Col>
          </Row>
        </ModalFooter>
      </Modal>
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={modalshow}>
        <ModalHeader toggle={() => setmodalshow(false)}>Warning</ModalHeader>
        <ModalBody>
          <div> Do you want to delete this data?</div>
        </ModalBody>
        <ModalFooter>
          <Button color="primary" className="pull-right" onClick={OnConfirmDeleteRow}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={() => setmodalshow(false)}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
      <Panel>
        <PanelHeader>All Rental Packages Lists</PanelHeader>
        <div className="table-responsive">
          <Table columns={columns} data={rows} />
        </div>
      </Panel>
    </div>
  );
};
