import React, { useState , useEffect} from 'react';
import { useTable, useSortBy, usePagination } from 'react-table';
import { PanelBody } from '../panel/panel';

export function Table({ columns, data }) {
  // Use the useTable Hook to send the columns and data to build the table
  const [loading, setloading] = useState(false);
  const {
    getTableProps, // table props from react-table
    getTableBodyProps, // table body props from react-table
    headerGroups, // headerGroups, if your table has groupings
    page, // rows for the table based on the data passed
    prepareRow, // Prepare the row (this function needs to be called for each row before getting the row props)

    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,
    state: { pageIndex, pageSize },
  } = useTable(
    {
      columns,
      data,
      initialState: { pageIndex: 0 },
    },
    useSortBy,
    usePagination
  );
  useEffect(() => {
    setloading(true);
    let timer = setTimeout(() => {
      setloading(false);
    }, 2000);
    return () => {
      clearTimeout(timer);
    };
  }, []);

  /* 
    Render the UI for your table
    - react-table doesn't have UI, it's headless. We just need to put the react-table props from the Hooks, and it will do its magic automatically
  */
  return (
    <>
      <PanelBody>
        <table className="table table-striped table-bordered" {...getTableProps()}>
          <thead>
            {headerGroups.map((headerGroup) => (
              <tr {...headerGroup.getHeaderGroupProps()}>
                {headerGroup.headers.map((column) => (
                  <th {...column.getHeaderProps(column.getSortByToggleProps())}>
                    <div className="d-flex" style={{ minWidth: '1%' }}>
                      <span>{column.render('Header')}</span>
                      <span className="ml-auto">
                        {column.sortable ? (
                          column.isSorted ? (
                            column.isSortedDesc ? (
                              <i className="fa fa-sort-down fa-fw f-s-14 text-blue"></i>
                            ) : (
                              <i className="fa fa-sort-up fa-fw f-s-14 text-blue"></i>
                            )
                          ) : (
                            <i className="fa fa-sort fa-fw f-s-14 opacity-3"></i>
                          )
                        ) : (
                          ''
                        )}
                      </span>
                    </div>
                  </th>
                ))}
              </tr>
            ))}
          </thead>

          <tbody {...getTableBodyProps()}>
            { page.length <= 0 ? (
              <tr style={{ textAlign: 'center', fontWeight: 'bold', fontSize: '18px' }}>
                <td colSpan={columns.length}>No matching records found</td>
              </tr>
            ) : (
              page.map((row, i) => {
                prepareRow(row);
                return (
                  <tr {...row.getRowProps()}>
                    {row.cells.map((cell) => {
                      return <td {...cell.getCellProps()}>{cell.render('Cell')}</td>;
                    })}
                  </tr>
                );
              })
            )}
          </tbody>
        </table>
      </PanelBody>
      {page.length <= 0 ? null : (
        <div className="d-flex align-items-center justify-content-center pb-2">
          <ul className="pagination mb-0">
            <li className="page-item">
              <button className="page-link" onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
                <i className="fa fa-angle-double-left"></i>
              </button>
            </li>
            <li className="page-item">
              <button className="page-link" onClick={() => previousPage()} disabled={!canPreviousPage}>
                <i className="fa fa-angle-left"></i>
              </button>
            </li>
            <li className="page-item d-flex align-items-center px-2">
              <div>
                Page{' '}
                <strong>
                  {pageIndex + 1} of {pageOptions.length}
                </strong>
              </div>
            </li>
            <li className="page-item">
              <button className="page-link" onClick={() => nextPage()} disabled={!canNextPage}>
                <i className="fa fa-angle-right"></i>
              </button>
            </li>
            <li className="page-item">
              <button className="page-link" onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
                <i className="fa fa-angle-double-right"></i>
              </button>
            </li>
          </ul>
          <div className="ml-3 mr-1">Go to page:</div>
          <div className="width-50 mx-2">
            <input
              className="form-control"
              type="number"
              defaultValue={pageIndex + 1}
              onChange={(e) => {
                const page = e.target.value ? Number(e.target.value) - 1 : 0;
                gotoPage(page);
              }}
            />
          </div>
          <div>
            <select
              className="form-control"
              value={pageSize}
              onChange={(e) => {
                setPageSize(Number(e.target.value));
              }}
            >
              {[5, 10, 15, 20, 25].map((pageSize) => (
                <option key={pageSize} value={pageSize}>
                  Show {pageSize}
                </option>
              ))}
            </select>
          </div>
        </div>
      )}
    </>
  );
}
