import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Panel, PanelHeader } from '../../components/panel/panel.jsx';
import { Button, Input, FormGroup, Label, Modal, ModalBody, ModalHeader, ModalFooter } from 'reactstrap';
import { Table } from '../../components/table/reactTable';
import { getData, patchData, deleteData, postData } from '../../services/Utils/DB/DB';
import { notification } from 'antd';
import { addNotification } from '../../components/ui-notifications';

const AdminRoles = () => {
  const [rows, setRows] = useState([]);
  const utoken = localStorage.getItem('utoken') || '';
  const [modalshow, setmodalshow] = useState(false);
  const [Editmsg, setEditmsg] = useState(false);
  const [ID, setID] = useState();
  const [focusAfterClose, setFocusAfterClose] = useState(true);
  const [description, setdescription] = useState('');
  const [name, setname] = useState('');
  const [displayName, setdisplayName] = useState('');
  const [open, setOpen] = useState(false);
  const [reloadStatus, setReloadStatus] = useState(0);
  const [loading, setloading] = useState(false);

  const toggle = () => {
    setOpen(!open);
    setdescription('');
    setdisplayName('');
    setname('');
  };

  useEffect(() => {
    setloading(true);
    getData('/roles', utoken)
      .then((data) => {
        if (data) {
          setRows(data);
          setloading(false);
          console.log(data);
        }
      })
      .catch((error) => {
        setloading(false);
        console.log(error);
      });
  }, [reloadStatus]);

  const OnDeleteRow = (id) => {
    setID(id);
    setmodalshow(true);
    // console.log(rows[id].uid);
  };

  const OnConfirmDeleteRow = () => {
    setloading(true);
    deleteData(`/roles?roleId=${rows[ID].roleId}`, utoken)
      .then((data) => {
        if (data) {
          // setRows(data);
          console.log(data);
          setRows([...rows.filter((item, index) => item !== rows[ID])]);
          console.log(data);
          setloading(false);
          addNotification('danger', 'DELETED', 'Role successfully deleted from the roles list.', 'bottom-right');
        }
      })
      .catch((error) => {
        setloading(false);
        addNotification('danger', 'Error', error, 'bottom-right');
      });

    setmodalshow(false);
  };

  const addRole = (e) => {
    e.preventDefault();

    const token = localStorage.getItem('utoken');
    setloading(true);
    var form_data = {
      displayName: displayName,
      name: name,
      description: description,
      type: 'Admin',
    };
    postData('/roles', JSON.stringify(form_data), token)
      .then((response) => {
        setloading(false);
        addNotification('success', 'ADDED', 'Role successfully deleted from the roles list.', 'bottom-right');
        setReloadStatus((prev) => prev + 1);
      })
      .catch((error) => {
        setloading(false);
        addNotification('danger', 'Error', error, 'bottom-right');
      });
    toggle();
  };

  const OnEditRow = async (id) => {
    setID(id);
    setEditmsg(true);
  };

  const OnConfirmEditRow = (e) => {
    e.preventDefault();
    if ((displayName === '') | (name === '') | (description === '')) {
      return notification.warn({
        message: <h1>Notification</h1>,
        description: `warn: complete all the fields to submit form.`,
        placement: 'bottomRight',
      });
    }
    setloading(true);
    var form_data = {
      roleId: rows[ID].roleId,
      displayName: displayName,
      name: name,
      description: description,
      type: 'Admin',
    };
    console.log(form_data);
    patchData(`/roles`, form_data, utoken)
      .then(({ data }) => {
        if (data) {
          let clone = [...rows];
          clone[ID] = data;
          setRows(clone);
          console.log(data);
          setloading(false);
          addNotification('warning', 'EDITED', 'Role successfully deleted.', 'bottom-right');
        }
      })
      .catch((error) => {
        addNotification('danger', 'Error', error, 'bottom-right');
        setloading(false);
        console.log(error.response);
      });
    setEditmsg(false);
  };
  const columns = React.useMemo(
    () => [
      {
        Header: 'Display Name',
        columns: [
          {
            Header: 'Display Name',
            accessor: 'displayName',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Created By',
        columns: [
          {
            Header: 'Created By',
            accessor: 'createdBy',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Date Of Creation',
        columns: [
          {
            Header: 'Date Of Creation',
            accessor: 'createdAt',
            sortable: true,
            Cell: ({ row }) => {
              return <span className="edit">{row.values.createdAt.split('T')[0]}</span>;
            },
          },
        ],
      },
      {
        Header: 'Edit',
        columns: [
          {
            Header: '',
            accessor: 'edit',
            sortable: false,
            Cell: ({ row }) => {
              return (
                <span className="edit">
                  <a className="btn btn-primary btn-icon btn-circle btn-sm">
                    <i className="fas fa-pencil-alt" onClick={() => OnEditRow(row.index)}></i>
                  </a>
                  <a className="btn btn-danger btn-icon btn-circle btn-sm">
                    <i className="fas fa-trash-alt" onClick={() => OnDeleteRow(row.index)}></i>
                  </a>
                </span>
              );
            },
          },
        ],
      },
    ],
    []
  );

  return (
    <div>
      <ol className="breadcrumb float-xl-right">
        <li className="breadcrumb-item">
          <Link to="/table/data">Home</Link>
        </li>
        <li className="breadcrumb-item">
          <Link to="/table/data">Tables</Link>
        </li>
        <li className="breadcrumb-item active">Data Tables</li>
      </ol>
      <h1 className="page-header">
        Adminstrators Roles <small>manage and approve the admin roles here.</small>
      </h1>
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={open}>
        <ModalHeader toggle={toggle}>Add Role</ModalHeader>
        <ModalFooter>
          <FormGroup className="w-100">
            <Label for="displayname">Display Name</Label>
            <Input
              type="text"
              name="displayName"
              id="displayname"
              value={displayName}
              onChange={(e) => setdisplayName(e.target.value)}
            />
          </FormGroup>
          <FormGroup className="w-100">
            <Label for="name">Name</Label>
            <Input type="text" name="name" id="name" value={name} onChange={(e) => setname(e.target.value)} />
          </FormGroup>
          <FormGroup className="w-100">
            <Label for="name">Description</Label>
            <Input
              type="text"
              name="description"
              id="description"
              value={description}
              onChange={(e) => setdescription(e.target.value)}
            />
          </FormGroup>

          <Button color="primary" className="pull-right" onClick={addRole}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={toggle}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={Editmsg}>
        <ModalHeader toggle={() => setEditmsg(false)}>Edit Role</ModalHeader>
        <ModalFooter>
          <FormGroup className="w-100">
            <Label for="displayname">Display Name</Label>
            <Input
              type="text"
              name="displayName"
              id="displayname"
              value={displayName}
              onChange={(e) => setdisplayName(e.target.value)}
            />
          </FormGroup>
          <FormGroup className="w-100">
            <Label for="name">Name</Label>
            <Input type="text" name="name" id="name" value={name} onChange={(e) => setname(e.target.value)} />
          </FormGroup>
          <FormGroup className="w-100">
            <Label for="name">Description</Label>
            <Input
              type="text"
              name="description"
              id="description"
              value={description}
              onChange={(e) => setdescription(e.target.value)}
            />
          </FormGroup>

          <Button color="primary" className="pull-right" onClick={OnConfirmEditRow}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={() => setEditmsg(false)}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={modalshow}>
        <ModalHeader toggle={() => setmodalshow(false)}>Warning</ModalHeader>
        <ModalBody>
          <div> Do you want to delete this data?</div>
        </ModalBody>
        <ModalFooter>
          <Button color="primary" className="pull-right" onClick={OnConfirmDeleteRow}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={() => setmodalshow(false)}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
      <Panel>
        <PanelHeader>
          All Admin Roles Lists
          <Button color="default" size="xs" className="mr-2 rounded-0 pull-right" onClick={toggle}>
            Add Role
          </Button>
        </PanelHeader>
        <div className="table-responsive">
          <Table columns={columns} data={rows} />
        </div>
      </Panel>
    </div>
  );
};

export default AdminRoles;
