import React, { useState, useEffect } from 'react';
import {
  Button,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  DropdownItem,
  DropdownToggle,
  UncontrolledButtonDropdown,
  DropdownMenu,
} from 'reactstrap';
import { Link } from 'react-router-dom';
import { Panel, PanelHeader, PanelBody } from '../../components/panel/panel.jsx';
import { getData, postData, deleteData, patchData } from '../../services/Utils/DB/DB';
import { Image } from 'antd';
import { EyeFilled } from '@ant-design/icons';
import { useHistory } from 'react-router-dom';
import { notification } from 'antd';
import { Table } from '../../components/table/reactTable';
import { addNotification } from '../../components/ui-notifications';

const SingleVideo = () => {
  const [rows, setRows] = useState([]);
  const utoken = localStorage.getItem('utoken') || '';
  const [modalshow, setmodalshow] = useState(false);
  const [Editmsg, setEditmsg] = useState(false);
  const [loading, setloading] = useState(false);
  const [reloadStatus, setReloadStatus] = useState(0);
  const [focusAfterClose] = useState(true);
  const [ID, setID] = useState();
  const history = useHistory();
  useEffect(() => {
    setloading(true);
    getData('/singles', utoken)
      .then((data) => {
        if (data) {
          setloading(false);
          setRows(data.data);
          console.log(data.data);
        }
      })
      .catch((error) => {
        setloading(false);
        console.log(error);
      });
  }, []);

  const columns = React.useMemo(
    () => [
      {
        Header: 'Name',
        columns: [
          {
            Header: 'Title',
            accessor: 'title',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Category',
        columns: [
          {
            Header: 'Category',
            accessor: 'category',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Genre',
        columns: [
          {
            Header: 'Genre',
            accessor: 'genre',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Content Creator',
        columns: [
          {
            Header: 'Content Creator',
            accessor: 'createdBy',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Date Of Creation',
        columns: [
          {
            Header: 'Date Of Creation',
            accessor: 'createdAt',
            sortable: true,
            Cell: ({ row }) => {
              return (
                <span style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                  <span>
                    {row.original.createdAt
                      .substring(8, 10)
                      .concat(row.original.createdAt.substring(4, 7), '-' + row.original.createdAt.substring(0, 4))}
                  </span>
                  {/* <span>{row.original.createdAt.substring(10,25)}</span> */}
                </span>
              );
            },
          },
        ],
      },
      {
        Header: 'Release Date',
        columns: [
          {
            Header: 'Release Date',
            accessor: 'releaseDate',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Media',
        columns: [
          {
            Header: 'Media',
            accessor: 'Media',
            sortable: false,
            Cell: ({ row }) => {
              return (
                <>
                  <div style={{ display: 'flex', justifyContent: 'space-between', width: '150px' }}>
                    <Image src={row.original.imageUrls.portrait} alt="" style={{ width: '50px', height: '50px' }} />
                    <Image src={row.original.imageUrls.landscape} alt="" style={{ width: '80px', height: '50px' }} />
                    {console.log('row Media:', row.original)}
                  </div>
                </>
              );
            },
          },
        ],
      },
      {
        Header: 'Status',
        columns: [
          {
            Header: 'Status',
            accessor: 'status',
            sortable: true,
            Cell: ({ row }) => {
              return (
                <>
                  {console.log(row.values.status)}
                  {row.values.status === 'CONVERSION_COMPLETE' ? (
                    <span className="label label-green">Converted</span>
                  ) : row.values.status === 'PUBLISHED' ? (
                    <span className="label label-green">Published</span>
                  ) : row.values.status === 'DISABLED' ? (
                    <span className="label label-danger">Not Published</span>
                  ) : (
                    <span className="label label-danger">Not Converted</span>
                  )}
                </>
              );
            },
          },
        ],
      },
      {
        Header: 'Edit',
        columns: [
          {
            Header: '',
            accessor: 'edit',
            sortable: false,
            Cell: ({ row }) => {
              return (
                <div className="btn-group m-r-5 m-b-5">
                  <button className="btn btn-sm btn-white" onClick={() => OnSelectRow(row.original)}>
                    <i className="fas fa-eye"></i> View
                  </button>
                  <UncontrolledButtonDropdown>
                    <DropdownToggle caret color="white btn-sm"></DropdownToggle>
                    <DropdownMenu right>
                      <DropdownItem onClick={() => OnEditRow(row.index)}>
                        <i className="fas fa-pencil-alt"></i> Edit
                      </DropdownItem>
                      <DropdownItem divider></DropdownItem>
                      <DropdownItem onClick={() => OnDeleteRow(row.index)}>
                        <i className="fas fa-trash"></i> Delete
                      </DropdownItem>
                    </DropdownMenu>
                  </UncontrolledButtonDropdown>
                </div>
              );
            },
          },
        ],
      },
    ],
    []
  );

  const OnSelectRow = (value) => {
    localStorage.setItem('SelectedVideoData', JSON.stringify(value));
    history.push('/content-management/single-video/details');
  };
  const OnEditRow = (id) => {
    history.push(`/content-management/single-video/edit/${id}`);
  };
  const OnDeleteRow = (id) => {
    setID(id);
    setmodalshow(true);
  };

  const OnDeleteConfirm = () => {
    const token = localStorage.getItem('utoken');
    setloading(true);
    // console.log(rows[ID].genreId);
    deleteData(`/singles?singleId=${rows[ID].singleId}`, token)
      .then((response) => {
        setRows([...rows.filter((item, index) => item !== rows[ID])]);
        setloading(false);
        addNotification('danger', 'Deleted', 'Video successfully removed from the video list.', 'bottom-right');
        setReloadStatus((prev) => prev + 1);
      })
      .catch((error) => {
        setloading(false);
        addNotification('danger', 'Error', error, 'bottom-right');
        setReloadStatus((prev) => prev + 1);
      });
    setmodalshow(false);
  };
  return (
    <div>
      <ol className="breadcrumb float-xl-right">
        <li className="breadcrumb-item">
          <Link to="/">Dashboard</Link>
        </li>
        <li className="breadcrumb-item">
          <Link to="/content-management/single-video/view">Videos</Link>
        </li>
        <li className="breadcrumb-item active">Single Videos</li>
      </ol>
      <h1 className="page-header">
        Single Video's <small>manage and approve the single videos here.</small>
      </h1>
      <Panel>
        <PanelHeader>All Single Videos Lists</PanelHeader>
        <div className="table-responsive">
          <Table columns={columns} data={rows} />
        </div>
      </Panel>
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={modalshow}>
        <ModalHeader toggle={() => setmodalshow(false)}>Warning</ModalHeader>
        <ModalBody>
          <div> Do you want to delete this data?</div>
        </ModalBody>
        <ModalFooter>
          <Button color="primary" className="pull-right" onClick={OnDeleteConfirm}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={() => setmodalshow(false)}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
    </div>
  );
};

export default SingleVideo;
