import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Panel, PanelHeader, PanelBody } from '../../components/panel/panel.jsx';
import { Button, Input, FormGroup, Label, Form, Row, Col, Modal, ModalBody, ModalHeader, ModalFooter } from 'reactstrap';
import { Table } from '../../components/table/reactTable';
import { getData, deleteData, patchData } from '../../services/Utils/DB/DB';
import { notification, Spin } from 'antd';
import SweetAlert from 'react-bootstrap-sweetalert';


export const SubscriptionPackages = () => {
  const [rows, setRows] = useState([]);
  const [modalshow, setmodalshow] = useState(false);
  const [ID, setID] = useState();
  const [focusAfterClose, setFocusAfterClose] = useState(true);
  const [loading, setloading] = useState(false);

  const utoken = localStorage.getItem('utoken') || '';

  useEffect(() => {
    setloading(!loading);
    getData('/subscriptiontypes', utoken)
      .then((data) => {
        if (data) {
          setRows(data);
          setloading(loading);
          console.log(data);
        }
      })
      .catch((error) => {
        setloading(loading);
        console.log(error);
      });
  }, []);

  const columns = React.useMemo(
    () => [
      {
        Header: 'Name',
        columns: [
          {
            Header: 'Name',
            accessor: 'subscriptionTypeDisplayName',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Type',
        columns: [
          {
            Header: 'Type',
            accessor: 'subscriptionType',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Duration',
        columns: [
          {
            Header: 'Duration',
            accessor: 'prices.duration',
            sortable: false,
          },
        ],
      },
      {
        Header: 'Price',
        columns: [
          {
            Header: 'Price',
            accessor: 'prices.price',
            sortable: false,
          },
        ],
      },
      {
        Header: 'Edit',
        columns: [
          {
            Header: '',
            accessor: 'edit',
            sortable: false,
            Cell: ({ row }) => {
              return (
                <button onClick={() => this.toggleSweetAlert('primary')} className="btn btn-primary m-r-5">Primary</button>,
                <span className="edit">
                  <a className="btn btn-danger btn-icon btn-circle btn-sm">
                    <i className="fas fa-trash-alt" onClick={() => OnDeleteRow(row.index)}></i>
                  </a>
                </span>
              );
            },
          },
        ],
      },
    ],
    []
  );

  const OnDeleteRow = (id) => {
    setID(id);
    setmodalshow(true);
  };

  const OnConfirmDeleteRow = () => {
    setloading(true);
    console.log(rows[ID].subscriptionTypeId);

    deleteData(`/subscriptiontypes?subscriptionTypeId=${rows[ID].subscriptionTypeId}`, utoken)
      .then((data) => {
        if (data) {
          // setRows(data);
          console.log(data);
          setRows([...rows.filter((item, index) => item !== rows[ID])]);
          console.log(data);
          setloading(false);
          notification.success({
            message: <h1>Notification</h1>,
            description: `success:Admin successfully removed from the subscription package list.`,
            placement: 'bottomRight',
          });
        }
      })
      .catch((error) => {
        setloading(false);
        notification.error({
          message: <h1>error</h1>,
          description: `${error}`,
          placement: 'bottomRight',
        });
        console.log(error);
      });

    setmodalshow(false);
  };



  return (
    <div>
      <ol className="breadcrumb float-xl-right">
        <li className="breadcrumb-item">
          <Link to="/">Dashboard</Link>
        </li>
        <li className="breadcrumb-item">
          <Link to="/subscription/view">Subscription Packages</Link>
        </li>
        <li className="breadcrumb-item active">View Subscription Packages</li>
      </ol>
      <h1 className="page-header">
        Subscription Packages <small>manage and approve the subscription packages here.</small>
      </h1>
      
      
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={modalshow}>
        <ModalHeader toggle={() => setmodalshow(false)}>Warning</ModalHeader>
        <ModalBody>
          <div> Do you want to delete this data?</div>
        </ModalBody>
        <ModalFooter>
          <Button color="primary" className="pull-right" onClick={OnConfirmDeleteRow}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={() => setmodalshow(false)}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
      <Panel>
        <PanelHeader>All Subscription Packages Lists</PanelHeader>
        <Table columns={columns} data={rows} />
      </Panel>
    </div>
  );
};
