import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import Select from 'react-select';
import { Panel, PanelBody, PanelFooter, PanelHeader } from '../../components/panel/panel';
import { Button, Input, FormGroup, Label, Form, Row, Col } from 'reactstrap';
import { postData } from '../../services/Utils/DB/DB';
import { notification } from 'antd';
import { useHistory } from 'react-router-dom';
import axios from "axios";
export const AddTax = () => {
  const [Country, country] = useState('');
  const [cType, cgstType] = useState('');
  const [cValue, cgstValue] = useState('');
  const [sType, sgstType] = useState('');
  const [sValue, sgstValue] = useState('');
  const [Currency, currency] = useState('');
  const history = useHistory();

  const onChangeCountry = (e) => {
    const Country = e.target.value;
    country(Country);
  };

  const onChangecgstType = (e) => {
    const cType = e.value;
    cgstType(cType);
  };

  const onChangecgstValue = (e) => {
    const cValue = e.target.value;
    cgstValue(cValue);
  };

  const onChangesgstType = (e) => {
    const sType = e.value;
    sgstType(sType);
  };

  const onChangesgstValue = (e) => {
    const sValue = e.target.value;
    sgstValue(sValue);
  };

  const onChangeCurrency = (e) => {
    const Currency = e.target.value;
    currency(Currency);
  };

  const AddTax = (e) => {
    e.preventDefault();
    if ((Country === '') | (cType === '') | (cValue === '') | (sType === '') | (sValue === '') | (Currency === '')) {
      return notification.warn({
        message: <h1>Notification</h1>,
        description: `warn: complete all the fields to submit form.`,
        placement: 'bottomRight',
      });
    }
    const token = localStorage.getItem('utoken');

    const form_data = {
      country: Country,
      cgstType: cType,
      cgstValue: cValue,
      sgstType: sType,
      sgstValue: sValue,
      currency:Currency
    };
    console.log(form_data);
    postData('/taxes',JSON.stringify(form_data), token)
      .then((response) => {
        notification.success({
          message: <h1>Notification</h1>,
          description: `success: ${Country}  successfully added to the tax list.`,
          placement: 'bottomRight',
        });
        history.push('/tax/view');
      })
      .catch((error) => {
        notification.error({
          message: <h1 style={{ color: 'red' }}>Error</h1>,
          description: `${error}.`,
          placement: 'bottomRight',
        });
      });
  };

  const options = [
    { value: 'Percentage', label: 'Percentage' },
    { value: 'Fixed', label: 'Fixed' },
  ];

  return (
    <div>
      <ol className="breadcrumb float-xl-right">
        <li className="breadcrumb-item">
          <Link to="/">Home</Link>
        </li>
        <li className="breadcrumb-item">
          <Link to="/tax/view">Taxes</Link>
        </li>
        <li className="breadcrumb-item active">Add Taxe</li>
      </ol>
      <h1 className="page-header">
        Add Tax <small>add a new tax here.</small>
      </h1>

      <Panel>
        <PanelHeader noButton={true}>Add Tax Form</PanelHeader>
        <PanelBody>
          <Row>
            <Col md="8">
              <div className="card">
                <div className="card-body">
                  <Form>
                    <Row>
                      <Col>
                        <FormGroup>
                          <Label for="country">Country</Label>
                          <Input type="text" name="country" id="country" value={Country} onChange={onChangeCountry} placeholder="India" />
                        </FormGroup>
                      </Col>
                    </Row>
                    <Row>
                      <Col>
                        <FormGroup>
                          <Label for="cType">CGST Type</Label>
                          <Select id="cType" options={options} onChange={onChangecgstType} />
                        </FormGroup>
                      </Col>
                      <Col>
                        <FormGroup>
                          <Label for="cValue">CGST Value</Label>
                          <Input type="text" name="cValue" id="cValue" value={cValue} onChange={onChangecgstValue} placeholder="6" />
                        </FormGroup>
                      </Col>
                    </Row>
                    <Row>
                      <Col>
                        <FormGroup>
                          <Label for="sType">SGST Type</Label>
                          <Select id="sType" options={options} onChange={onChangesgstType} />
                        </FormGroup>
                      </Col>
                      <Col>
                        <FormGroup>
                          <Label for="sValue">SGST Value</Label>
                          <Input type="text" name="sValue" id="sValue" value={sValue} onChange={onChangesgstValue} placeholder="6" />
                        </FormGroup>
                      </Col>
                    </Row>
                    <Row>
                      <Col>
                        <FormGroup>
                          <Label for="currency">Currency</Label>
                          <Input type="text" name="currency" id="currency" value={Currency} onChange={onChangeCurrency} placeholder="INR" />
                        </FormGroup>
                      </Col>                
                      
                    </Row>
                  </Form>
                </div>
                <div className="card-footer">
                  <Row>
                    <Col>
                      <div className="pull-right">
                        <button onClick={AddTax} className="btn btn-primary mr-3">
                          Submit
                        </button>
                        <button
                          className="btn btn-secondary"
                          onClick={() => history.push('/tax/view')}
                        >
                          Cancel
                        </button>
                      </div>
                    </Col>
                  </Row>
                </div>
              </div>
            </Col>
          </Row>
        </PanelBody>
      </Panel>
    </div>
  );
};
