import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Panel, PanelHeader, PanelBody } from './../../components/panel/panel.jsx';
import { Button, Input, FormGroup, Label, Form, Row, Col, Modal, ModalBody, ModalHeader, ModalFooter } from 'reactstrap';
import makeData from './../make-data';
import { getData, patchData, deleteData, postData } from './../../services/Utils/DB/DB';
import { notification } from 'antd';
import axios from 'axios';
import { Table } from '../../components/table/reactTable';
import { addNotification } from '../../components/ui-notifications';
const Banners = () => {
  const [title, settitle] = useState('');
  const [displayOrder, setdisplayOrder] = useState('');
  const [ddvalue, setddvalue] = useState('');
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const toggle = () => setDropdownOpen((prevState) => !prevState);
  const [rows, setRows] = useState([]);
  const utoken = localStorage.getItem('utoken') || '';
  const [loading, setloading] = useState(false);
  const [modalshow, setmodalshow] = useState(false);
  const [Editmsg, setEditmsg] = useState(false);
  const [ID, setID] = useState();
  const [focusAfterClose, setFocusAfterClose] = useState(true);
  useEffect(() => {
    setloading(true);
    getData('/banners', utoken)
      .then(async (data) => {
        if (data) {
          const Arr = [];
          Object.values(data).map((i) => {
            const item = {
              title: i.title,
              activeimage: i.activeImage,
              type: i.itemType,
              contentCreator: i.createdBy,
              createdAt: i.createdAt,
              category: i.category,
              status: i.status,
              image: i.imageUrls.landscape,
              bannerId: i.bannerId,
            };
            Arr.push(item);
          });
          setRows(Arr);
          setloading(false);
          console.log(rows);
          console.log('Manage', data);
        }
      })
      .catch((error) => {
        setloading(false);
        console.log(error);
      });
  }, []);

  const OnSelectValue = (e) => {
    setddvalue(e);
  };

  const onChangetitle = (e) => {
    const first = e.target.value;
    settitle(first);
  };
  const onChangedisplayOrder = (e) => {
    const last = e.target.value;
    setdisplayOrder(last);
  };
  const [imgLandscapeName, setImgLandscapeName] = useState('');
  const [imgLandscapeFile, setImgLandscapeFile] = useState(null);
  const [landscapeFile, setLandscapeFile] = useState(null);
  const [invalidImgLandscape, setInvalidImgLandscape] = useState(false);
  const handleLandscapeChange = ({ target: { files } }) => {
    const cancel = !files.length;
    if (cancel) return;

    setImgLandscapeFile(URL.createObjectURL(files[0]));
    setLandscapeFile(files[0]);
    const [{ size, name }] = files;
    setImgLandscapeName(name);
    setInvalidImgLandscape(false);
  };

  const OnDeleteRow = (id) => {
    setID(id);
    setmodalshow(true);
    // console.log(rows[id]);
    // console.log(rows)
  };

  const OnConfirmDeleteRow = () => {
    setloading(true);
    console.log(ID);
    console.log(rows[ID].bannerId);
    deleteData(`/banners?bannerId=${rows[ID].bannerId}`, utoken)
      .then((data) => {
        if (data) {
          console.log(data);
          setRows([...rows.filter((item, index) => item !== rows[ID])]);
          console.log(data);
          setloading(false);
          addNotification('success', 'ADDED', 'Banner successfully added to the banner list.', 'bottom-right');
        }
      })
      .catch((error) => {
        setloading(false);
        addNotification('danger', 'Error', error, 'bottom-right');
        console.log(error);
      });

    setmodalshow(false);
  };
  const OnEditRow = async (id) => {
    setID(id);
    setEditmsg(true);
  };

  const OnConfirmEditRow = (e) => {
    e.preventDefault();
    if ((title === '') | (displayOrder === '') | (ddvalue === '')) {
      return notification.warn({
        message: <h1>Notification</h1>,
        description: `warn: complete all the fields to submit form.`,
        placement: 'bottomRight',
      });
    }
    const token = localStorage.getItem('utoken');
    setloading(true);
    var form_data = {
      bannerid: rows[ID].bannerid,
      displayOrder: displayOrder,
      title: title,
      activeImage: ddvalue,
      target: {},
    };
    console.log(form_data);

    patchData(`/banners`, form_data, utoken)
      .then(({ data }) => {
        if (data) {
          const form_data = {
            s3Keys: [{ key: `${data.imageS3Keys.landscape}`, contentType: 'image/jpg' }],
            contentBucket: data.contentBucket,
          };
          //   form_data.s3Keys.push({ key: `${data.imageS3Keys.landscape}`, contentType: 'image/jpg' });
          //   form_data.s3Keys.push({ key: `${data.imageS3Keys.portrait}`, contentType: 'image/jpg' });
          console.log(form_data);
          postData('/presign', JSON.stringify(form_data), token)
            .then((res) => {
              axios
                .put(res.data[0].signedUrl, landscapeFile)
                .then((response) => console.log(response))
                .catch((error) => console.log(error));

              console.log('presign:', res);
              console.log('presign:', res.data[0].signedUrl);
            })
            .catch((error) => console.log('presign:', error));
          let clone = [...rows];
          clone[ID] = data;
          setRows(clone);
          console.log(data);
          setloading(false);
          addNotification('warning', 'EDITED', 'Banner info successfully edited', 'bottom-right');
        }
      })
      .catch((error) => {
        setloading(false);
        addNotification('danger', 'Error', error, 'bottom-right');
        console.log(error.response);
      });
    setEditmsg(false);
  };

  const columns = React.useMemo(
    () => [
      {
        Header: 'Image',

        columns: [
          {
            Header: 'Image',
            accessor: 'image',
            sortable: false,
            Cell: ({ row }) => {
              return (
                <span className="edit">
                  {/* {console.log(row.original.image)} */}
                  <img src={row.original.image} alt={row.original.title} className="img-rounded height-30" />
                </span>
              );
            },
          },
        ],
      },
      {
        Header: 'Title',
        columns: [
          {
            Header: 'Title',
            accessor: 'title',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Active Image',
        columns: [
          {
            Header: 'Active Image',
            accessor: 'activeimage',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Type',
        columns: [
          {
            Header: 'Type',
            accessor: 'type',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Date Of Creation',
        columns: [
          {
            Header: 'Date Of Creation',
            accessor: 'createdAt',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Category',
        columns: [
          {
            Header: 'Category',
            accessor: 'category',
            sortable: true,
          },
        ],
      },
      {
        Header: 'Status',
        columns: [
          {
            Header: 'Status',
            accessor: 'status',
            sortable: true,
            Cell: ({ row }) => {
              return (
                <span className="edit">
                  {row.original.status === 'Enabled' ? (
                    <span className="label label-green">{row.original.status}</span>
                  ) : (
                    <span className="label label-danger">{row.original.status}</span>
                  )}
                </span>
              );
            },
          },
        ],
      },
      {
        Header: 'Edit',
        columns: [
          {
            Header: '',
            accessor: 'edit',
            sortable: false,
            Cell: ({ row }) => {
              return (
                <span className="edit">
                  <a className="btn btn-primary btn-icon btn-circle btn-sm">
                    <i className="fas fa-pencil-alt" onClick={() => OnEditRow(row.index)}></i>
                  </a>
                  <a className="btn btn-danger btn-icon btn-circle btn-sm">
                    <i className="fas fa-trash-alt" onClick={() => OnDeleteRow(row.index)}></i>
                  </a>
                </span>
              );
            },
          },
        ],
      },
    ],
    []
  );

  return (
    <div>
      <ol className="breadcrumb float-xl-right">
        <li className="breadcrumb-item">
          <Link to="/">Dashboard</Link>
        </li>
        <li className="breadcrumb-item">
          <Link to="/content-management/video-category">Video Categories</Link>
        </li>
        <li className="breadcrumb-item active">View Categories</li>
      </ol>
      <h1 className="page-header">
        Video Banners <small>manage and edit the video banners here.</small>
      </h1>
      <Modal returnFocusAfterClose={focusAfterClose} isOpen={modalshow}>
        <ModalHeader toggle={() => setmodalshow(false)}>Warning</ModalHeader>
        <ModalBody>
          <div> Do you want to delete this data?</div>
        </ModalBody>
        <ModalFooter>
          <Button color="primary" className="pull-right" onClick={OnConfirmDeleteRow}>
            Submit
          </Button>
          <Button color="default" className="pull-right ml-2" onClick={() => setmodalshow(false)}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
      <Panel>
        <PanelHeader noButton={true}>All Video Banners Lists</PanelHeader>
        <div className="table-responsive">
          <Table columns={columns} data={rows} />
        </div>
      </Panel>
    </div>
  );
};

export default Banners;
