export async function uploadVideo(data, videoFile, token) {
  const form_dataVideo = {
    s3Keys: [{ key: `${data.videoS3Keys.original}`, contentType: 'video/mp4' }],
    contentBucket: data.contentBucket,
  };
  console.log(form_dataVideo, 'video Called');
  const presignData = await presignURL(form_dataVideo, token);
  console.log(presignData);
  uploadFile(presignData.data[0].signedUrl, videoFile);
}

export async function uploadImages(data, File, FileType, token) {
  if (File !== null) {
    var form_data = {
      s3Keys: [{ key: `${data.imageS3Keys[`${FileType}`]}`, contentType: 'image/jpg' }],
      contentBucket: data.contentBucket,
    };
    console.log(form_data, `${FileType} , ${File}`);
    const presignData = await presignURL(form_data, token);
    uploadFile(presignData.data[0].signedUrl, File);
  }
}

export function uploadFile(url, file) {
  console.log('Hello');
  console.log(url);
  axios
    .put(url, file)
    .then((response) => console.log(response, 'DATA UPLOADED'))
    .catch((error) => console.log(error));
}

export const presignURL = async (data, Token) => {
  try {
    const result = await postData('/presign', JSON.stringify(data), Token);
    return result;
  } catch (error) {}
};
