import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { Panel, PanelBody, PanelFooter, PanelHeader } from '../../components/panel/panel';
import { Button, Input, FormGroup, Label, Form, Row, Col } from 'reactstrap';
import { Multiselect } from 'multiselect-react-dropdown';
import InputMask from 'react-input-mask';
import { postData } from '../../services/Utils/DB/DB';
import { notification } from 'antd';
import { useHistory } from 'react-router-dom';
import { addNotification } from '../../components/ui-notifications';
const data = [
  { role: 'Admin', id: 1 },
  { role: 'Content Approver', id: 3 },
  { role: 'Super Admin', id: 2 },
];

export const AddAdmin = () => {
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [user_roles, setUserRoles] = useState([]);
  const history = useHistory();

  const onChangeFirstName = (e) => {
    const first = e.target.value;
    setFirstName(first);
  };
  const onChangeLastName = (e) => {
    const last = e.target.value;
    setLastName(last);
  };
  const onChangeEmail = (e) => {
    const email = e.target.value;
    setEmail(email);
  };
  const onChangePhone = (e) => {
    const phone = e.target.value;
    setPhone(phone);
  };
  const handleMultiselect = (selectedList, selectedItem) => {
    const str = selectedItem.role.toLowerCase();
    const newstr = str.split(' ').join('-');
    const updatedRoles = [...user_roles, newstr];
    console.log(updatedRoles);
    setUserRoles(updatedRoles);
    console.log(updatedRoles);
  };
  console.log(user_roles);
  const handleRemoval = (selectedList, removedItem) => {
    const str = removedItem.role.toLowerCase();
    const newstr = str.split(' ').join('-');
    const updatedRole = user_roles.filter((role) => role !== newstr);
    setUserRoles(updatedRole);
  };
  const AddNewAdmin = (e) => {
    e.preventDefault();
    if ((firstName === '') | (lastName === '') | (phone === '') | (user_roles === '') | (email === '')) {
      return notification.warn({
        message: <h1>Notification</h1>,
        description: `warn: complete all the fields to submit form.`,
        placement: 'bottomRight',
      });
    }
    const token = localStorage.getItem('utoken');
    console.log(user_roles);
    const form_data = {
      firstName: firstName,
      lastName: lastName,
      phoneNumber: phone,
      roles: user_roles,
      email: email.toLocaleLowerCase(),
    };

    console.log(form_data);
    postData('/admins', form_data, token)
      .then((response) => { 
        addNotification('success', 'ADDED', 'Administrator successfully deleted from the list.', 'bottom-right');
        history.push('/adminstrator/view-adminstrators');
      })
      .catch((error) => {
        addNotification('danger', 'Error', error, 'bottom-right');
      });
  };
  const [options] = useState(data);
  return (
    <div>
      <ol className="breadcrumb float-xl-right">
        <li className="breadcrumb-item">
          <Link to="/">Home</Link>
        </li>
        <li className="breadcrumb-item">
          <Link to="/subscriber/view-subscribers">Adminstrator</Link>
        </li>
        <li className="breadcrumb-item active">Add Adminstrator</li>
      </ol>
      <h1 className="page-header">
        Add Adminstrator <small>add a new adminstrator here.</small>
      </h1>

      <Panel>
        <PanelHeader noButton={true}>Add Adminstrator Form</PanelHeader>
        <PanelBody>
          <Row>
            <Col md="8">
              <div className="card">
                <div className="card-body">
                  <Form>
                    <Row>
                      <Col>
                        <FormGroup>
                          <Label for="first">First Name</Label>
                          <Input
                            type="text"
                            name="first"
                            id="first"
                            // placeholder="Product Name"
                            value={firstName}
                            onChange={onChangeFirstName}
                            // className={!isInvalidName ? "" : "is-invalid"}
                            required
                          />
                        </FormGroup>
                      </Col>
                      <Col>
                        <FormGroup>
                          <Label for="last">Last Name</Label>
                          <Input
                            type="text"
                            name="last"
                            id="last"
                            // placeholder="Product Name"
                            value={lastName}
                            onChange={onChangeLastName}
                            required
                            // className={!isInvalidName ? "" : "is-invalid"}
                          />
                        </FormGroup>
                      </Col>
                    </Row>
                    <Row>
                      <Col md="8">
                        <FormGroup>
                          <Label for="email">Email</Label>
                          <Input
                            type="email"
                            name="email"
                            id="email"
                            // placeholder="Product Name"
                            value={email}
                            onChange={onChangeEmail}
                            required
                            // className={!isInvalidName ? "" : "is-invalid"}
                          />
                        </FormGroup>
                      </Col>
                      <Col>
                        <FormGroup>
                          <Label for="phone">Phone No.</Label>
                          <InputMask
                            id="phone"
                            mask="+\91 999 999 9999"
                            maskChar="_"
                            className="form-control"
                            value={phone}
                            onChange={onChangePhone}
                            required
                          ></InputMask>
                        </FormGroup>
                      </Col>
                    </Row>
                    <Row>
                      <Col>
                        <FormGroup>
                          <Label for="role">Admin Roles</Label>
                          <Multiselect
                            id="role"
                            onRemove={handleRemoval}
                            onSelect={handleMultiselect}
                            options={options}
                            displayValue={'role'}
                          />
                        </FormGroup>
                      </Col>
                    </Row>
                  </Form>
                </div>
                <div className="card-footer">
                  <Row>
                    <Col>
                      <div className="pull-right">
                        <button onClick={AddNewAdmin} className="btn btn-primary mr-3">
                          Submit
                        </button>
                        <button className="btn btn-secondary" onClick={() => history.push('/adminstrator/view-adminstrators')}>
                          Cancel
                        </button>
                      </div>
                    </Col>
                  </Row>
                </div>
              </div>
            </Col>
          </Row>
        </PanelBody>
      </Panel>
    </div>
  );
};
